import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
const {
	COMPONENT_CONNECTED,
	COMPONENT_DISCONNECTED
} = actionTypes;
import {preUserData} from 'sn-uxpage-presource';
import {
	handleOnComponentConnected,
	handleOnComponentDisconnected,
	updateStateWithPresenceData,
	workItemSubscribed,
	updateWorkItemsHandler,
	readAwaWorkItem,
	interactionUpdated,
	interactionUnassigned
} from './actionHandlers';
import {
	agentPresenceAMBChannel,
	agentChannelPresenceAMBChannel,
	presenceStateAMBChannel,
	serviceChannelAMBChannel,
	groupMemberAMBChannel,
	readPresenceStateData,
	workItemSubscription,
	executeAWAWorkitemReadHandler,
	awaWorkItemRejectionAMBChannel,
	subscribeInteractionChanges
} from './dataSources';

import {
	SUBSCRIBE_AGENT_PRESENCE,
	SUBSCRIBE_AGENT_CHANNEL_AVAIABILITY,
	SUBSCRIBE_PRESENCE_STATE,
	SUBSCRIBE_SERVICE_CHANNEL,
	SUBSCRIBE_GROUP_MEMBER,
	READ_PRESENCE_STATE_DATA, 
	UPDATE_STATE_WITH_PRESENCE_DATA,
	WORK_ITEM_SUBSCRIPTION,
	WORK_ITEMS_SUBSCRIBED,
	WORK_ITEMS_CHANGED,
	SUBSCRIBE_WORK_ITEM_REJECT,
	WORK_ITEM_REJECT_EVENT,
	EXECUTE_AWA_WORK_ITEM_READ,
	WORK_ITEM_REJECT_SUCCESS,
	SUBSCRIBE_INTERACTION_CHANGES,
	INTERACTION_UPDATED
} from './constants';

export default createCustomElement('sn-openframe-wrapper', {
	renderer: {
		type: snabbdom,
		view: null
	},
	properties: {
		user:{}
	},
	behaviors: [preUserData],
	actionHandlers: {
		[COMPONENT_CONNECTED]: handleOnComponentConnected,
		[COMPONENT_DISCONNECTED]: handleOnComponentDisconnected,
		[READ_PRESENCE_STATE_DATA]: readPresenceStateData,
		[UPDATE_STATE_WITH_PRESENCE_DATA]: updateStateWithPresenceData,
		[SUBSCRIBE_AGENT_PRESENCE]: agentPresenceAMBChannel,
		[SUBSCRIBE_AGENT_CHANNEL_AVAIABILITY]: agentChannelPresenceAMBChannel,
		[SUBSCRIBE_PRESENCE_STATE]: presenceStateAMBChannel,
		[SUBSCRIBE_SERVICE_CHANNEL]: serviceChannelAMBChannel,
		[SUBSCRIBE_GROUP_MEMBER]: groupMemberAMBChannel,
		// work item handlers.
		[WORK_ITEM_SUBSCRIPTION]: workItemSubscription,
		[WORK_ITEMS_SUBSCRIBED]: workItemSubscribed,
		[WORK_ITEMS_CHANGED]: updateWorkItemsHandler,
		[SUBSCRIBE_WORK_ITEM_REJECT]: awaWorkItemRejectionAMBChannel,
		[WORK_ITEM_REJECT_EVENT]: readAwaWorkItem,
		[EXECUTE_AWA_WORK_ITEM_READ]:executeAWAWorkitemReadHandler,
		[WORK_ITEM_REJECT_SUCCESS]: updateWorkItemsHandler,
		// interaction handlers
		[SUBSCRIBE_INTERACTION_CHANGES] : subscribeInteractionChanges,
		[INTERACTION_UPDATED] : interactionUpdated
	}
});