import { subscribeChannels } from './dataHandlers';
import { get } from 'lodash';
import { READ_PRESENCE_STATE_DATA, PRESENCE_STATE_WATCHER, WORK_ITEM_SUBSCRIPTION, INTERACTION_EVENT_MAP, WORK_ITEMS_CHANGED, 
	     SUBSCRIBE_INTERACTION_CHANGES, INTERACTION_WATCHER, EXECUTE_AWA_WORK_ITEM_READ, WORK_ITEM_REJECT_SUCCESS, REJECTION_CONTEXT } from './constants';
import { extractPresenceDataFromPayload, getPresencePayload, getCurrentUserId, dispatchWorkItems, extractInteractionData, extractRejectedWorkItem  } from './util';

export const handleOnComponentConnected = ({ state, action, dispatch }) => {
		let user = state.properties.user ? state.properties.user.sys_id :getCurrentUserId(state.behaviors);
		dispatch(READ_PRESENCE_STATE_DATA);
		subscribeChannels(dispatch, true, user, '');
		dispatch(WORK_ITEM_SUBSCRIPTION, {
			subscribe: true,
			userId:user
		});	
		dispatch(SUBSCRIBE_INTERACTION_CHANGES, {
			subscribe: true,
			userId:user
		});
};

export const handleOnComponentDisconnected = ({ dispatch }) => {
	subscribeChannels(dispatch, false);
};

export const updateStateWithPresenceData = ({ state, action, dispatch, updateState }) => {
	const presenceData = extractPresenceDataFromPayload(action.payload);
	const presencePayload = getPresencePayload(action.payload);
	let presenceState = {
		presence: presencePayload
	}
	let result = {
		presenceData:presenceState
	}
	dispatch(PRESENCE_STATE_WATCHER, result);

};

