import cuid from 'cuid';
import { REPORT_GROUP_CONFIG_TYPE } from '../constants';
import { safelyGetValue } from './context';

const generateId = elements =>
	elements.map(element =>
		safelyGetValue(['type'], element) == REPORT_GROUP_CONFIG_TYPE
			? {
				...element,
				id: cuid(),
				reportGroupConfigurations: safelyGetValue(
					['reportGroupConfigurations'],
					element
				).map(report => ({ ...report, id: cuid() }))
			  }
			: { ...element, id: cuid() }
	);

//in some older browsers window.undefined can be assigned values
const isUndefined = (value) => {
	const undefined = void(0);
	return value === undefined;
};

export { generateId,isUndefined };
