import {nowContentTree} from "@servicenow/now-content-tree";
import {nowLoader} from "@servicenow/now-loader";
import {nowToggle} from "@servicenow/now-toggle";
import { t } from 'sn-translate';
import { PREVIEW_RECORD } from './actions';

export const view = (state, {dispatch, updateState}) => {
	const { items, itemPath,
		parentTree
	} = state.properties;
	const { loadingPath } = state;
	
	const { clickedId, parentViewOnly } = state;
	let { expanded, parentExpanded } = state;
	if (!expanded) {
		expanded = state.properties.expanded;
		updateState({
			operation: 'set',
			path: 'expanded',
			value: expanded
		});
	}

	if (!parentExpanded) {
		parentExpanded = state.properties.parentExpanded;
		updateState({
			operation: 'set',
			path: 'parentExpanded',
			value: parentExpanded
		});
	}

	if (items == null || items.length == 0) return <div><now-loader label={t('Loading')}></now-loader></div>;

	if (clickedId) {
		dispatch(PREVIEW_RECORD, {table: 'customer_account', sys_id: clickedId});
	}

	let treeView = parentViewOnly ? parentTree : items;
	let expandedPath = parentViewOnly ? parentExpanded : expanded;
	let selectedPath = [];

	// If the expanded path is empty don't try to slice it
	if (expandedPath.length > 0) selectedPath = expandedPath[expandedPath.length - 1].slice();
	if (parentViewOnly) selectedPath.push(state.properties.sysId);
	
	return (
        <div className="sn-account-hierarchy">
			<div className='parent-view-container'><label id='parentViewOnly'>{t('Parent View')}</label><now-toggle className='parent-view-label' aria-labelledby='parentOnlyView' config-aria={{'aria-label': t('Parent View Only')}} checked></now-toggle></div>
			<now-content-tree items={treeView} size="md" expanded-items={expandedPath} loading-items={[{path: loadingPath}]}></now-content-tree>
		</div>
    );
};
