
import { UPDATE_STATE_WITH_PRESENCE_DATA, READ_PRESENCE_STATE_DATA, WORK_ITEMS_CHANGED, WORK_ITEMS_SUBSCRIBED, WORK_ITEM_REASON_SUCCEEDED, 
         WORK_ITEM_REASON_FAILED, INTERACTION_UPDATED, WORK_ITEM_REJECT_EVENT, WORK_ITEM_REJECT_SUCCESS } from './constants';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {createAmbSubscriptionEffect} from '@servicenow/ui-effect-amb'

const workItemChannelPath = '/awa/work_item/:userId';

const agentPresenceStatesGraphlQLDatasource_read = `query {
  GlideAWA_Query {
    agent_presence {
      current_presence_state_id
      state_icon_type
      name
      sys_id
    }
    presence_state {
      name
      show_channels
      sys_id
      order
      available_to_receive_work_items
      agent_channel_availability {
        name
        restrict_update
        service_channel_id
        sys_id
        available
      }
    }
  }
}`;


const REJECT_REASON = `
query {
  GlideRecord_Query {
    awa_work_item_rejection(queryConditions: "rejected_by=^work_item=") {
      _results {
        reason{
          value
        }
        rejected_by{
          value
      }
      sys_updated_on{
          value
      }
      work_item{
        value
      }
  }
}
}
}`;

const awaWorkItemReadQuery =
`query{
  GlideRecord_Query {
    awa_work_item_rejection(queryConditions: "sys_id={{sys_id}}") {
      _results {
        work_item {
          value
        }
        reason {
          _reference {
            reason {
              value
            }
            sys_id {
              value
            }
          }
        }
        _query {
          GlideRecord_Query {
            awa_work_item(queryConditions: "sys_id=$$parent.work_item$$") {
              _results {
                size {
                  value
                }
                document_id {
                  value
                }
                document_table {
                  value
                }
                state {
                  value
                }
                queue {
                  _reference {
                    service_channel {
                      _reference {
                        name {
                          value
                        }
                        sys_id {
                          value
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}`;


export const readPresenceStateData = createGraphQLEffect(agentPresenceStatesGraphlQLDatasource_read, {
	successActionType: UPDATE_STATE_WITH_PRESENCE_DATA
});

export const agentPresenceAMBChannel = createAmbSubscriptionEffect('/rw/default/awa_agent_presence/:filter', {
	messageReceivedActionType: READ_PRESENCE_STATE_DATA
});

export const agentChannelPresenceAMBChannel = createAmbSubscriptionEffect('/rw/default/awa_agent_channel_availability/:filter', {
	messageReceivedActionType: READ_PRESENCE_STATE_DATA
});

export const presenceStateAMBChannel = createAmbSubscriptionEffect('/rw/default/awa_presence_state/:filter', {
	messageReceivedActionType: READ_PRESENCE_STATE_DATA
});

export const serviceChannelAMBChannel = createAmbSubscriptionEffect('/rw/default/awa_service_channel/:filter', {
	messageReceivedActionType: READ_PRESENCE_STATE_DATA
});

export const groupMemberAMBChannel = createAmbSubscriptionEffect('/rw/default/sys_user_grmember/:filter', {
	messageReceivedActionType: READ_PRESENCE_STATE_DATA
});

export const workItemSubscription = createAmbSubscriptionEffect(workItemChannelPath, {
	subscribeSucceededActionType: WORK_ITEMS_SUBSCRIBED,
	messageReceivedActionType: WORK_ITEMS_CHANGED
});

export const workItemFirstRead = createGraphQLEffect(workItemChannelPath, {
	successActionType: WORK_ITEMS_CHANGED
});

export const rejectReasonWorkItem = createGraphQLEffect(REJECT_REASON, {
	successActionType: WORK_ITEM_REASON_SUCCEEDED,
	errorActionType: WORK_ITEM_REASON_FAILED
});

export const awaWorkItemRejectionAMBChannel = createAmbSubscriptionEffect('/rw/default/awa_work_item_rejection/:filter', {
  messageReceivedActionType: WORK_ITEM_REJECT_EVENT
});

export const executeAWAWorkitemReadHandler = createGraphQLEffect(awaWorkItemReadQuery, {
  templateVarList: ['sys_id'],
  successActionType: WORK_ITEM_REJECT_SUCCESS
})

// Interaction 
const interactionChannelPath = '/openframe/interaction/:userId';
export const subscribeInteractionChanges = createAmbSubscriptionEffect( interactionChannelPath, {
  messageReceivedActionType: INTERACTION_UPDATED
});
