const extractPresourceConfig = (window) => {
	let openframeConfig = get(window, 'ux_globals.presource["sn-component-openframe:openFrameConfig"]', false);
	return get(openframeConfig, 'data.AppOpenframe_Query');
};

const extractPathAndHostOrigin = (config) => {
	let hostOrigin = get(config, 'config.url', false);
	let path = '';
	if (hostOrigin.isAbsolute) {
		path = hostOrigin.scheme + '://' + hostOrigin.authority;
		hostOrigin = hostOrigin.stringValue;
	} else {
		path = window.location.origin;
		hostOrigin = `${path}/${hostOrigin.stringValue}`;
	}
	return { path, hostOrigin };
};

const initializePostMessage = (state, pathAndHostOriginData, dispatch, updateState) => {
	state.hostOrigin = pathAndHostOriginData.hostOrigin;
	if (!state.createdPm) {
		let thisState = {
			props:{
				dispatch: dispatch
			}
		};
		state.pm = create(events, thisState, {
			origin: pathAndHostOriginData.path,
			constructMessage,
			deconstructMessage,
			transform
		});
		state.createdPm = true;
		updateState({
			...state
		});
	}
};

const handlePostMessageInit = (coeffects) => {
	const { state, dispatch, updateState } = coeffects;
	const {isLegacy, configuration = {}} = state.properties;
	const config = isLegacy ? extractPresourceConfig(window) : configuration;
	if (get(config, 'config.url', false)) {
		let pathAndHostOriginData = extractPathAndHostOrigin(config);
		initializePostMessage(state, pathAndHostOriginData, dispatch, updateState);
		updateState({
			...state,
			initializeWrapper: true
		});
	}
};

const handleStartOpenframeLoading = (coeffects) => {
	const { state, dispatch, updateState } = coeffects;
	const {isLegacy} = state.properties;
	const config = state.properties.isLegacy ? extractPresourceConfig(window) : state.properties.configuration;
	if (get(config, 'config.url', false)) {
		dispatch(OPENFRAME_ACTIONS.FETCH_OPENFRAMECONFIG_EFFECT, config);
		updatePhoneOnToolbar(dispatch, config.showFrame, MAP_ICON_NAME[config.config['navIconClass']], isLegacy);
		dispatch(OPENFRAME_ACTIONS.RECORD_WATCHER_SUBSCRIPTION, {
			subscribe: true
		});
	}
};

