	const { state, action, dispatch, updateState } = coeffects;
	let contactDetails = get(action, 'payload.data.GlideRecord_Query.customer_contact._results[0]');
	let entityData = contactContextHelper(contactDetails);
	let interaction = contactInteractionHelper(contactDetails);
