/** Openframe component local handlers - START */
export const openframeLocalIframeLoad = (coeffects) => {
	const { state, action, dispatch, updateProperties, updateState } = coeffects;
	state.local.contentWindowInit = true;
	state.pm.target(get(action, 'payload.contentWindow'));
	let openframe_init = get(state, 'openframe_init');
	if (openframe_init && openframe_init.data) {
		state.pm.post(OPENFRAME_ACTIONS.OPENFRAME_RESPONSE, frameResponse(OPENFRAME_ACTIONS.OPENFRAME_RESPONSE, openframe_init.data));
		state.openframe_init = {};
	} else {
		waitOpenframeInit(coeffects);
	}
	updateState({
		...state,
		position : get(state, 'properties.position')
	});
	updateProperties({ openframeInitStatus: true });
};

export const waitOpenframeInit = (coeffects) => {
	let { state, updateState, updateProperties, dispatch } = coeffects;
	let retryTime = 3;
	let retryInterval = 500;
	let interval = setInterval(() => {
		if (retryTime > 0) {
			dispatch(OPENFRAME_ACTIONS.OPENFRAME_INIT_DELAYED, {});
			retryTime--;
		} else {
			clearInterval(interval);
		}
	}, retryInterval);

}

// For some reason if the timing of openframe init is out of sync with init request
// there is a timeout. This is a workaround fix.
const openframeInitDelayHandler = (coeffects) => {
	let {updateState, updateProperties, state} = coeffects;
	let openframe_init = get(state, 'openframe_init');
	if(openframe_init && openframe_init.data && !state.openframeInitSuccess){
		state.pm.post(OPENFRAME_ACTIONS.OPENFRAME_RESPONSE, frameResponse(OPENFRAME_ACTIONS.OPENFRAME_RESPONSE, openframe_init.data));
		state.openframe_init = {}
		updateState({
			...state,
			position : get(state, 'properties.position')
		});
		updateProperties({ openframeInitStatus: true });
		updateState({openframeInitSuccess: true});
	}
}

const workspaceNavSelected = (coeffects) => {
	const { state, dispatch, action, updateProperties } = coeffects;
	if (get(action, 'payload.id') == 'cti' && get(action, 'payload.select') != state.local.visible) {
		state.local.visible = get(action, 'payload.select');
		updateProperties({visible: get(action, 'payload.select')});
		let fireEvent = get(action, 'payload.select') ? OPENFRAME_ACTIONS.OPENFRAME_SHOW : OPENFRAME_ACTIONS.OPENFRAME_HIDE;
		if (get(state, 'pm', false)) {
			dispatch(fireEvent);
		}
	}
};

const openframeFrameMode = (coeffects) => {
	const { state, action, dispatch, updateState } = coeffects;
	state.showMinimize.value = (get(action, 'payload.message') === 'expand') ? false : true;
	updateState({
		...state
	});
	let frameEvent = state.showMinimize.value ? 'openframe_collapse' : 'openframe_expand';
	state.pm.post(frameEvent, frameResponse(frameEvent));
};

const outgoingCall = (coeffects) => {
	const { state, action } = coeffects;
	if (get(action, 'payload.phoneNumber')) {
		let metaData = { 'phoneNumber': get(action, 'payload.phoneNumber'), taskId: '' };
		let data = { 'metaData': metaData, 'data': [] };
		let payload = { 'type': 'OUTGOING_CALL', 'data': data };
		let context = { 'payload': payload, 'method': 'openframe_communication' };
		updateState({
			...state
		});
		state.pm.post(context.method, frameResponse(context.method, context.payload));
	}
};

const openframeRerender = ({state, updateState}) => {
	updateState({
		...state
	});
};
/** Openframe component local handlers - END */

/** AWA Agent presence state watcher - START */
const agentPresenceStateWatcher = ({state, action}) => {
	const awaPresenceStateData = get(action, 'payload');
	let context = { 'result': awaPresenceStateData.presenceData };
	state.pm.post(OPENFRAME_ACTIONS.OPENFRAME_AWA_AGENT_PRESENCE, frameResponse(OPENFRAME_ACTIONS.OPENFRAME_AWA_AGENT_PRESENCE, context));
};


const handleWorkitemAccepted = ({state, action}) => {
	state.pm.post(OPENFRAME_ACTIONS.OPENFRAME_AWA_WORKITEM_ACCEPTED, frameResponse(OPENFRAME_ACTIONS.OPENFRAME_AWA_WORKITEM_ACCEPTED, {result: action.payload}));
};

const handleWorkitemRejected = ({state, action}) => {
	state.pm.post(OPENFRAME_ACTIONS.OPENFRAME_AWA_WORKITEM_REJECTED, frameResponse(OPENFRAME_ACTIONS.OPENFRAME_AWA_WORKITEM_REJECTED, {payload: action.payload}));
};

const handleWorkitemOffered = ({state, action}) => {
	state.pm.post(OPENFRAME_ACTIONS.OPENFRAME_AWA_WORKITEM_OFFERED, frameResponse(OPENFRAME_ACTIONS.OPENFRAME_AWA_WORKITEM_OFFERED, {result: action.payload}));
};
/** AWA Agent presence state watcher - END */

/** Agent Off Interaction watcher - START */
const interactionWatcher = ({state, action}) => {
	const interactionPayload = get(action, 'payload');
	let context = { 'result' : interactionPayload };
	state.pm.post(OPENFRAME_ACTIONS.OPENFRAME_AGENT_OFF_INTERACTION, frameResponse(OPENFRAME_ACTIONS.OPENFRAME_AGENT_OFF_INTERACTION, context));
};
/** Agent Off Interaction watcher - END */

