import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {view} from './view';
import { actionHandlers } from './actions/index.js';
import styles from './styles.scss';
/**
 * A column layout generator component which can host components
 * 
 * ```
 * <sn-customer-information />
 * ``` 
 * @seismicElement sn-customer-information
 * @summary A column layout generator component which can host components
 * 
 */
createCustomElement('sn-customer-information', {
	renderer: { type: snabbdom },
	view,
	initialState : {
		layout : {
			cards_info : []
		},
		isLoading : false,
		isError : false,
		start : 0,
		pageSize : 6
	},
	properties: {
		/**
		 * The table representing the current context 
		 * @type {string}
		 */
		contextTable: {
			default: '',
			schema : {type : 'string'}
		},
		/**
		 * The sys_id of the current context
		 * @type {string}
		 */
		contextId: {
			default: '',
			schema : {type : 'string'}
		},
		/**
		 * The table name from action model fields
		 * @type {string}
		 */
		table: {
			default: ''
		},
		/**
		 * fields object from action model fields
		 * @type {object}
		 */
		fields : {
			default : {}
		}
	},
	actionHandlers,
	styles
});
