import {FACET_CONSTANTS, FEED_CONSTANTS} from '../constants';
import moment from 'moment';
import {INTERNAL_DATE_TIME_FORMAT} from '../constants';

export const getFacetAndFeedKeys = (facetIds, record_id) => {
	let facet_key = FACET_CONSTANTS.ALL_ACTIVITY_HEAD;
	let feed_key = FEED_CONSTANTS.FEED_HEAD;
	if(facetIds && record_id){
		facet_key = facetIds;
		feed_key = record_id;
	}
	else if(facetIds){
		facet_key = facetIds;
	}
	return {
		facet_key,
		feed_key
	};
};

export const getFeedCount = (facetIds, record_id, feedInfo) => {
	let {facet_key, feed_key} = getFacetAndFeedKeys(facetIds, record_id);
	if(feedInfo && feedInfo[facet_key] 
         && feedInfo[facet_key][feed_key])
		return feedInfo[facet_key][feed_key].activitiesCount;
	return 0;
};


export const getStartAndEndInUTC = (startDate, endDate, timeZoneOffset) => {
	let start = startDate;
	let end = endDate;
	start = moment.utc(start).startOf('day').utcOffset(-1 * timeZoneOffset).format(INTERNAL_DATE_TIME_FORMAT);
	end = moment.utc(end).endOf('day').utcOffset(-1 * timeZoneOffset).format(INTERNAL_DATE_TIME_FORMAT);
	return {start, end};
};