import {getProperty} from 'sn-uxpage-presource';
import { get } from 'lodash';
import {
	INITIALIZE_POST_MESSAGE,
	OPENFRAME_COMPONENT_WORKSPACE_LOAD_RETRY_COUNT, START_OPENFRAME_LOADING, NAV_SELECTED, VIEW_PHONE
} from './constants';
import {findTabbableNodes} from "@servicenow/behavior-key-binding";

var checkPresourceConfiguration = () => {
	return new Promise((resolve, reject) => {
		const maxAttempts = getProperty(OPENFRAME_COMPONENT_WORKSPACE_LOAD_RETRY_COUNT) || 3;
		let config = null;
		let attempts = 0;
		const retryTime = 1000;

		let interval = setInterval(() => {
			const openframeConfig = get(window ,'ux_globals.presource["sn-component-openframe:openFrameConfig"]', false);
			config = get(openframeConfig, 'data.AppOpenframe_Query');
			if(get(config, 'showFrame')==='true') {
				if (get(config, 'config.url', false)) {
					clearInterval(interval);
					return resolve({
						dispatchInit: true
					});
				}
			}

			attempts++;
			if(attempts>=maxAttempts){
				clearInterval(interval);
				return reject({
					dispatchInit: false
				});
			}
		}, retryTime);
	});
};

function getFocusedElement(host) {
	return host.shadowRoot.activeElement;
}

function afterNextRender() {
	return new Promise((resolve) =>
		setTimeout(() => window.requestAnimationFrame(resolve), 0)
	);
}

export function handleOnBootstrap(coeffects) {
	let {dispatch, state} = coeffects;
	if(state.properties.configuration){
		dispatch(INITIALIZE_POST_MESSAGE);
		dispatch(START_OPENFRAME_LOADING);
	} else {
		checkPresourceConfiguration()
		.then(() => {
			dispatch(INITIALIZE_POST_MESSAGE);
			dispatch(START_OPENFRAME_LOADING);
		}, ()=>{});
	}

}

export async function handleOnPropertyChange({action, updateProperties, state, dispatch}) {
	const {host, name, value} = action.payload;
	const {number, table, sysId, source, isLegacy} = state.properties;
	if (host.visible && !getFocusedElement(host)) {
		await afterNextRender();
		const tabbableNodes = findTabbableNodes(host);
		if (tabbableNodes[0]) {
			tabbableNodes[0].focus();
		}
	}

	if(name === 'openPhone'){
		if(isLegacy){
			dispatch(VIEW_PHONE, value);
		}
	}

	if (name === 'opened' ) {
		updateProperties({
			selectedNav: {
				id: 'cti',
				select: value
			}
		});
	}
	if(name === 'selectedNav'){
		dispatch(NAV_SELECTED, value);
	}

	if(!isLegacy && name === 'source' && (value ==='form_field' || value ==='ribbon')){
		dispatch(VIEW_PHONE, {number: number, sysId: sysId, table: table, source: source});
	}
}
