import {nowContentTree} from "@servicenow/now-content-tree";
import {nowHeading} from "@servicenow/now-heading";
import { t } from 'sn-translate';

const view = (state, dispatch) => {
    
    return (
        <div className="sn-hierarchy">
        <span className="heading">{t(state.properties.label)}</span>
        {state.fullTree.length!=0?
            <now-content-tree showDividers={true} items={state.searchedTree.length > 0 || state.properties.sysId? state.searchedTree : state.fullTree}  size='md'> </now-content-tree>
            :
            <p className="empty-tree">{t("No records to display")}</p>
        }
        </div>
    );
}

export default view;