import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import {LOAD_HIERARCHY_DATA, LOAD_SEARCHED_TREE} from './constants';
import {actionHandlers} from './actions';
import style from './snProductModelHierarchy.scss'

createCustomElement('sn-product-model-hierarchy', {
	renderer: {
		type: snabbdom,
		view
	},

	initialState: {
		maxDepth:2,
		productsDetails : [],
		expandedItems : [],
		currentTooltip: {ref: null, content: null, opened: false}
	},

	properties: {
		sysId : {default : "null"},
		tableName: {default:'cmdb_m2m_model_component'},
		childFieldName: {default: "child"},
		parentFieldName: {default:'parent'},
		maxChildren : {default: 5},
		query: {default: ""}
	},

	actionHandlers,

	onBootstrap(host, dispatch){
		dispatch(LOAD_HIERARCHY_DATA, { 
			tableName: 'cmdb_m2m_model_component',
			childFieldName: "child", 
			parentFieldName: 'parent',
			parentProductSysId: "",
			maxChildren: 5,
			currentChildrenCount : 0,
			maxDepth: 2,
			query: "",
			selectedSoldProductId: host.sysId
		});
	},

	styles: style
});
