import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import style from './styles.scss'
import properties from './properties'
import view from './view'
const {COMPONENT_BOOTSTRAPPED} =actionTypes
const showSubmitButton = (returnTexts,notesheader)=>{
	if(Object.entries(returnTexts).length === 0 && returnTexts.constructor === Object){
		for(var i=0;i<notesheader.length;i++){
			if(notesheader[i].mandatory){
					return false;
			}
		}
	}
	for(var i=0;i<notesheader.length;i++){
		if(notesheader[i].mandatory && !returnTexts[notesheader[i].fieldName]){
			return false;
		}
		if(notesheader[i].mandatory && !returnTexts[notesheader[i].fieldName].trim()){
				return false;
		}
	}
	return true;
}

createCustomElement('sn-template-modal-worknotes', {
	renderer: {type: snabbdom},
	initialState: {
		returnTexts:{}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({state,dispatch}) => {
			var set=true;
			//disable the submit button initially if any mandatory field is set true
			for(var i=0;i<state.properties.notesheader.length;i++){
				if(state.properties.notesheader[i].mandatory){
					set=false;
					break;
				}
			}
			dispatch('SN_SCRIPTED_MODAL#BUTTON_ENABLED_SET', set)
		},
		'DEBOUNCE_TEXTING': {
		  modifier: {name: 'debounce', delay: 50},
		  effect({state,dispatch,updateState,action}) {
					let {name,value} = action.payload
					updateState({
						operation:`set`,
						path:`returnTexts[${name}]`,
						value: value
					});  

					let returnText=state.returnTexts
					returnText[name]=value
					var submitbutton=showSubmitButton(returnText,state.properties.notesheader)
					submitbutton?dispatch('SN_SCRIPTED_MODAL#DATA_SET', {returnTexts: state.returnTexts}):null
					dispatch('SN_SCRIPTED_MODAL#BUTTON_ENABLED_SET', submitbutton)
		  }
	}},
	properties,
	view,
	styles:style
});
