import {ACTIONS, APIS, INTERNAL_DATE_TIME_FORMAT} from '../../constants';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {getFacetAndFeedKeys, getFeedCount} from '../../utils/feedUtils';
import moment from 'moment';

const fetchFeed = createHttpEffect(APIS.FEED_API, {
	method: 'GET',
	queryParams:[
		'context',
		'context_instance',
		'facets',
		'record_id',
		'stFrom',
		'last',
		'start_date',
		'end_date',
		'before'
	],
	successActionType: ACTIONS.FEED_DATA_FETCH_SUCCEEDED,
	errorActionType: ACTIONS.FEED_DATA_FETCH_FAILED
});

const onFeedFetchFail = ({updateState}) => {
	updateState({
		path: 'data.isFeedLoading',
		value: false,
		operation: 'set'
	});
	console.error('Feed Fetch Failed ');
};

const onFeedFetch = ({action: {
	payload,
	meta
}, updateState, state}) => {
	updateState({
		path: 'kickStartElement',
		value: [],
		operation: 'set'
	});
	let {facet_key, feed_key} = getFacetAndFeedKeys(meta.facets, meta.record_id);
	//feed show more clicked
	if(meta.feedUpdating){
		updateState({
			path: 'data.feedInfo',
			value: {
				...state.data.feedInfo,
				[facet_key]: {
					...state.data.feedInfo[facet_key],
					[feed_key]: {
						activitiesCount: state.data.feedInfo[facet_key][feed_key].activitiesCount
											+ payload.result.activities.length
					}
				}
			},
			operation: 'set',
			shouldRender: false
		});

		updateState({
			path: 'data.feed.activities',
			value: payload.result.activities,
			operation: 'concat',
			shouldRender: false
		});
		updateState({
			path: 'data.feed.hasMoreRecords',
			value: payload.result.hasMoreRecords,
			operation: 'set',
			shouldRender: false
		});
		updateState({
			path: 'data.isFeedShowMoreLoading',
			value: false,
			operation: 'set'
		});
	}
	//facet changed
	else if(meta.facetUpdated){
		updateState({
			path: 'data.feedInfo',
			value: {
				...state.data.feedInfo,
				[facet_key]: {
					...state.data.feedInfo[facet_key],
					[feed_key]: {
						activitiesCount: payload.result.activities.length
					}
				}
			},
			operation: 'set',
			shouldRender: false
		});
		updateState({
			path: 'data.feed.activities',
			value: payload.result.activities,
			operation: 'set',
			shouldRender: false
		});
		updateState({
			path: 'data.feed.hasMoreRecords',
			value: payload.result.hasMoreRecords,
			operation: 'set',
			shouldRender: false
		});
		updateState({
			path: 'data.selectedFacets',
			value: meta.facets? meta.facets: '',
			operation: 'set',
			shouldRender: false
		});
		updateState({
			path: 'data.selectedRecordId',
			value: meta.record_id? meta.record_id: '',
			operation: 'set',
			shouldRender: false
		});
		updateState({
			path: 'data.isFeedLoading',
			value: false,
			operation: 'set'
		});
	}
	else{
		updateState({
			path: 'data.feedInfo',
			value: {
				...state.data.feedInfo,
				[facet_key]: {
					...state.data.feedInfo[facet_key],
					[feed_key]: {
						activitiesCount: payload.result.activities.length
					}
				}
			},
			operation: 'set',
			shouldRender: false
		});
		updateState({
			path: 'data.feed',
			value: {
				activities: payload.result.activities,
				hasMoreRecords: payload.result.hasMoreRecords
			},
			operation: 'set',
			shouldRender: false
		});
		updateState({
			path: 'data.isFeedLoading',
			value: false,
			operation: 'set'
		});
	}
};

const updateFeed = ({
	properties,
	state: {data: {selectedFacet, feedInfo}, startDateInUTC, endDateInUTC, before},
	dispatch,
	action: {payload: {title, facets, sys_id: record_id}},
	updateState
}) => {

	let {
		context,
		contextId: context_instance,
		activitiesListSize
	} = properties;
	let facetIds = facets? facets.join(','): '';
	let feedActivitiesLength = getFeedCount(
		facetIds,
		record_id,
		feedInfo
	);
	updateState({
		path: 'feedTitle',
		value: title,
		operation: 'set'
	});
	if(selectedFacet == 0){
		before = moment.utc().format(INTERNAL_DATE_TIME_FORMAT);
		updateState({
			path: 'before',
			value: before,
			operation: 'set',
			shouldRender: false
		});
		//Reset the show all selection as this loads all the facets once again.
		updateState({
			path: 'data.showAllChildrenFor',
			value: [],
			operation: 'set',
			shouldRender: false
		});

		dispatch(ACTIONS.FACET_DATA_FETCH_REQUESTED, {
			context_instance,
			context,
			start_date: startDateInUTC,
			end_date: endDateInUTC,
			before: before
		});
	}
	dispatch(ACTIONS.FEED_DATA_FETCH_REQUESTED, {
		context_instance,
		context,
		facets: facetIds,
		record_id: record_id,
		stFrom: 0,
		last: (feedActivitiesLength==0)? activitiesListSize: feedActivitiesLength,
		start_date: startDateInUTC,
		end_date: endDateInUTC,
		before: before
	}, {
		facets: facetIds,
		record_id: record_id,
		facetUpdated: true
	});
};

const onRequest = ({action: {
	payload,
	meta
}, updateState, dispatch}) => {
	if(!meta.feedUpdating)
		updateState({
			path: 'data.isFeedLoading',
			value: true,
			operation: 'set'
		});
	dispatch(ACTIONS.FEED_DATA_FETCH_INITIATED, payload, {
		feedUpdating: meta.feedUpdating,
		facetUpdated: meta.facetUpdated,
		facets: meta.facets,
		record_id: meta.record_id
	});
};

const fetchMoreActivities = ({state, dispatch, properties, updateState}) => {
	let {data, startDateInUTC, endDateInUTC, before} = state;
	let {contextId, context, activitiesListSize } = properties;
	let {selectedFacets, selectedRecordId, feedInfo} = data;
	updateState({
		path: 'data.isFeedShowMoreLoading',
		operation: 'set',
		value: true
	});
	let feedActivitiesLength =  getFeedCount(
		selectedFacets,
		selectedRecordId,
		feedInfo
	);
	dispatch(ACTIONS.FEED_DATA_FETCH_REQUESTED, {
		'context_instance': contextId,
		'context': context,
		'facets':selectedFacets,
		'recrord_id': selectedRecordId,
		'stFrom':feedActivitiesLength,
		'last': feedActivitiesLength + activitiesListSize,
		'start_date': startDateInUTC,
		'end_date': endDateInUTC,
		'before': before
	}, {
		feedUpdating: true,
		facets: selectedFacets,
		record_id: selectedRecordId
	});
};

const openRecord = ({action: {payload}, dispatch}) => {
	if(payload && payload.table && payload.sys_id){
		dispatch(ACTIONS.OPEN_RECORD, payload);
	}
};

const actionHandlers = {
	[ACTIONS.FEED_DATA_FETCH_REQUESTED]: onRequest,
	[ACTIONS.FEED_DATA_FETCH_INITIATED]: fetchFeed,
	[ACTIONS.FEED_DATA_FETCH_FAILED]: onFeedFetchFail,
	[ACTIONS.FEED_DATA_FETCH_SUCCEEDED]: onFeedFetch,
	[ACTIONS.FILTER_APPLIED]: updateFeed,
	[ACTIONS.FEED_SHOW_MORE_CLICKED]: fetchMoreActivities,
	[ACTIONS.FEED_LINK_CLICKED]: openRecord
};

export default actionHandlers;
