import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import actionHandlers from './actions';
import style from './snSoldProductForm.scss';
import { BUILD_QUERY } from './constants';

createCustomElement('sn-sold-product-form', {
	renderer: {
		type: snabbdom,
		view
	},

	properties: {
		tableName: {default:'sn_install_base_sold_product'},
		fields: {default: 'number,product'},
		sysId:{default: null, onChange(newValue, oldValue, dispatch) {
			dispatch(BUILD_QUERY,{});
		}},
	},
	onBootstrap(host, dispatch){
		dispatch(BUILD_QUERY,{});
	},
	actionHandlers,
	styles: style
});