import '@servicenow/now-empty-state';
import '@devsnc/sn-layout';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

import { renderList } from './components/list';
import { renderRecord } from './components/record';
import { renderVisual, renderVisualGroup } from './components/report';
import { getSlotNameFn, generateModel } from './utils/modelGenerator';
import {
	LIST_CONFIG_TYPE,
	RECORD_CONFIG_TYPE,
	REPORT_CONFIG_TYPE,
	REPORT_GROUP_CONFIG_TYPE,
} from './constants';

const generateCards = ({ layout, cards_info }) => {
	const getSlotName = getSlotNameFn(layout);
	return (
		cards_info &&
		cards_info.map((card, index) => {
			return card.type == LIST_CONFIG_TYPE ? (
				renderList({ slotName: getSlotName(index), listJSON: card })
			) : card.type == REPORT_CONFIG_TYPE ? (
				renderVisual({ slotName: getSlotName(index), visualJSON: card })
			) : card.type == REPORT_GROUP_CONFIG_TYPE ? (
				renderVisualGroup({
					slotName: getSlotName(index),
					visualGroupJSON: card,
				})
			) : card.type == RECORD_CONFIG_TYPE ? (
				renderRecord({ slotName: getSlotName(index), recordJSON: card })
			) : (
				<now-empty-state
					className="sn-customer-information-list"
					type="empty"
					variant="small"
					slot={getSlotName(index)}
					key={cards_info.id}
				/>
			);
		})
	);
};

export const view = (
	{ isError, isLoading, layout: layoutJSON }
) => (
	<div className="sn-customer-information">
		{isError ? (
			<now-empty-state type="empty" />
		) : (
			<Fragment>
				<sn-layout model={generateModel(layoutJSON.layout)}>
					{layoutJSON ? generateCards(layoutJSON) : undefined}
				</sn-layout>
				<div className="sn-customer-information-loader">
					{isLoading ? <now-loader size="lg" /> : undefined}
				</div>
			</Fragment>
		)}
	</div>
);
