import { map } from 'lodash';

const BEHAVIOR_NAME = 'childrenWithSlot';

const mutationObserver = new WeakMap();

/**
 * The childrenWithSlot behavior has a list of the child elements
 * that have a 'slot' attribute
 */
export default {
	name: BEHAVIOR_NAME,
	initialState: {
		slots: new Map()
	},
	actionHandlers: {
		UPDATE_SLOT_LIST({ action, state, updateState }) {
			const { added, removed } = action.payload;
			const res = new Map(state.behaviors[BEHAVIOR_NAME].slots);
			removed.map(r => {
				const count = res.get(r);
				count > 1 ? res.set(r, count - 1) : res.delete(r);
			});
			added.map(a => res.set(a, res.has(a) ? res.get(a) + 1 : 1));
			updateState({
				path: `behaviors.${BEHAVIOR_NAME}.slots`,
				operation: 'set',
				value: res
			});
		},
		CLEAR_SLOTS_MAP({ state, updateState }) {
			const res = state.behaviors[BEHAVIOR_NAME].slots;
			res.clear();
			updateState({
				path: `behaviors.${BEHAVIOR_NAME}.slots`,
				operation: 'set',
				value: res
			});
		}
	},
	actions: {
		UPDATE_SLOT_LIST: {
			private: true
		},
		CLEAR_SLOTS_MAP: {
			private: true
		}
	},
	onConnect(el, { dispatch }) {
		if (!mutationObserver.has(el)) {
			const observer = new MutationObserver(mutations => {
				const changes = mutations.reduce(
					(acc, m) => {
						if (
							m.type === 'attributes' &&
							m.attributeName === 'slot' &&
							m.target.slot !== m.oldValue
						) {
							acc.added.push(m.target.slot);
							acc.removed.push(m.oldValue);
						} else if (m.type === 'childList') {
							if (m.removedNodes.length > 0) {
								map(m.removedNodes, n => acc.removed.push(n.slot));
							}
							if (m.addedNodes.length > 0) {
								map(m.addedNodes, n => acc.added.push(n.slot));
							}
						}
						return acc;
					},
					{ added: [], removed: [] }
				);
				if (
					dispatch &&
					(changes.added.length > 0 || changes.removed.length > 0)
				) {
					dispatch('UPDATE_SLOT_LIST', changes);
				}
			});
			observer.observe(el, {
				childList: true,
				attributes: true,
				subtree: true,
				attributeOldValue: true
			});
			mutationObserver.set(el, observer);
		}
		dispatch('UPDATE_SLOT_LIST', {
			added: map(el.querySelectorAll('[slot]'), e => e.slot),
			removed: []
		});
	},
	onDisconnect(el, dispatch) {
		mutationObserver.delete(el);
		dispatch('CLEAR_SLOTS_MAP');
	}
};
