import { SLOT_NAME_PREFIX, LAYOUT_DELIMITER } from '../constants';

const getSlotNameFromList = (index, columnList) =>
	SLOT_NAME_PREFIX + (index % columnList.length);

const getSlotNameFn = layoutFormat => index =>
	getSlotNameFromList(
		index,
		layoutFormat && layoutFormat.split(LAYOUT_DELIMITER)
	);

const generateColumns = columnList =>
	columnList &&
	columnList.map((columnWidth, index) => ({
		type: 'column',
		size: columnWidth,
		slotName: getSlotNameFromList(index, columnList)
	}));

const generateModel = columnSplitString => ({
	default: {
		type: 'rowColumn',
		root: {
			type: 'row',
			columns: generateColumns(
				columnSplitString && columnSplitString.split(LAYOUT_DELIMITER)
			)
		}
	}
});

export { generateModel, getSlotNameFn };
