import { GRID_SIZE } from './utils';
import { map, sumBy, get, toNumber } from 'lodash';

export function getSize(column) {
	const size = toNumber(get(column, 'size', 1));
	return Number.isNaN(size) ? 1 : size;
}

export const transformColumns = columns => {
	const totalSize = sumBy(columns, getSize);
	return map(columns, c => Math.round((getSize(c) / totalSize) * GRID_SIZE));
};
