import {ACTIONS, APIS} from '../../constants';
import {createHttpEffect} from '@servicenow/ui-effect-http';

const _updateState = (updateState, path, value, shouldNotRender) => {
	updateState({
		path,
		value,
		operation: 'set',
		shouldRender: (!shouldNotRender) ? true : false
	});
};

const processFacets = (facets, stateData, level = 0, coefficient = 1) => {
	facets.map((facet, index) => {
		facet._facetIdx = level * coefficient + index + 1;
		facet._facetId = facet.sys_id || facet.facet ||
			('facet_g_'+ (facet.facets ? facet.facets[0]: facet.name));
		if (facet.children) {
			processFacets(facet.children, stateData, facet._facetIdx, 100);
		}
		if(stateData.showAllChildrenFor.indexOf(facet._facetId) > -1) {
			facet.childsDisplayLimit = 100000;
		}
	});
};

const onFacetFetch = (
	{
		state: {data},
		action: {
			payload: {result: result},
			meta: {request: {params}}
		}, updateState
	}) => {
	processFacets(result, data);
	let {facets: facetIds} = params;
	if (facetIds && facetIds.length > 0) {
		facetIds = facetIds.split(',');
		let loadingFacet = data.loadingChildFacets[facetIds[0]];
		loadingFacet.children = [...result[0].children];
		loadingFacet.activity_count = result[0].activity_count;

		_updateState(updateState, 'data.loadingChildFacets.'+facetIds[0], undefined, true);
		_updateState(updateState, 'data.expandedFacet', loadingFacet._facetId, false);
	} else {

		_updateState(updateState, 'data.facets', [...result], true);
		_updateState(updateState, 'data.loadingFacets', false, false);

		//reset the focus.
	/*
		if (data.nextFocusableKey == 0) {
			setTimeout(() => {
				const elements = host.shadowRoot.querySelectorAll(
					'.facet-item:not(.facet-hidden)');
				if (elements) elements[0].focus();
			}, 100);
			_updateState(updateState, 'data.nextFocusableKey', '');
		} */
	}
};

const onFacetFetchFail = ({updateState}) => {
	console.error('Facet Fetch Failed');
	_updateState(updateState, 'data.loadingFacets', false);
};

const fetchFacets = createHttpEffect(APIS.FACET_API, {
	method: 'GET',
	pathParams: ['context', 'context_instance'],
	queryParams: ['facets', 'start_date', 'end_date', 'get_activity_count', 'lazy_load', 'before'],
	successActionType: ACTIONS.FACET_DATA_FETCH_SUCCEEDED,
	errorActionType: ACTIONS.FACET_DATA_FETCH_FAILED
});

const onRequest = (
	{
		state: {properties: {showCountOnFacet, loadChildFacetsOnDemand}},
		action: {payload}, updateState, dispatch
	}) => {
	const {facets} = payload;
	if (facets && facets.length > 0) {
		payload.facets = facets.join(); // <== CSV
	} else
		_updateState(updateState, 'data.loadingFacets', true);

	dispatch(ACTIONS.FACET_DATA_FETCH_INITIATED, {
		...payload,
		//Avoiding continuous loading.
		context: payload.context ? payload.context : 'null',
		context_instance: payload.context_instance ? payload.context_instance : 'null',
		get_activity_count: !loadChildFacetsOnDemand && showCountOnFacet,
		lazy_load: loadChildFacetsOnDemand && !payload.feedFilterApplied && (!payload.facets || payload.facets.length == 0)
	});
};

const onFacetSelection =
	({state: {data: {selectedFacet}}, action: {payload}, updateState, dispatch}) => {
		if (selectedFacet != payload.selectedFacet) {
			_updateState(updateState, 'data.selectedFacet', payload.selectedFacet);
			dispatch(ACTIONS.FILTER_APPLIED, {...payload, facets: [payload.facet]});
		}
	};

const onFacetGroupSelection = (
	{
		state: {
			data,
			properties: {loadChildFacetsOnDemand, contextId, context},
			startDateInUTC,
			endDateInUTC
		},
		action: {payload}, updateState, dispatch
	}) => {

	const {selectedFacet, expandedFacet, loadingChildFacets} = data;
	const {facet} = payload;
	const {name, title, facets: facetIds, is_dynamic, _facetId} = facet;
	// Avoid duplicate selections.
	if (selectedFacet == _facetId && _facetId != 0)
		return;
	let newData = {
		...data,
		selectedFacet: _facetId,
		expandedFacet: _facetId
	};
	if (_facetId != expandedFacet)
		newData.random = 0;

	if(_facetId == 0)
		_updateState(updateState, 'data.loadingChildFacets', {}, true);

	/* if (_facetId == 0)
		newData.nextFocusableKey = 0; */

	_updateState(updateState, 'data.selectedFacet', newData.selectedFacet, true);
	_updateState(updateState, 'data.expandedFacet', newData.expandedFacet);
	dispatch(ACTIONS.FILTER_APPLIED, {title: title || name, facets: facetIds});

	if (is_dynamic && loadChildFacetsOnDemand) {
		let lcf = {...loadingChildFacets};
		lcf[facetIds[0]] = facet;
		_updateState(updateState, 'data.loadingChildFacets', lcf);
		dispatch(ACTIONS.FACET_DATA_FETCH_REQUESTED, {
			facets: facetIds,
			context, context_instance: contextId,
			name,
			start_date: startDateInUTC,
			end_date: endDateInUTC
		}
		);
	}
};

const onFacetGroupDeselection = ({updateState}) => {
	_updateState(updateState, 'data.expandedFacet', '');
};

const onShowMoreRequest = (
	{state: {data}, properties: {childFacetsListSize}, action: {payload}, updateState}) => {
	const {facet} = payload;
	facet.childsDisplayLimit = (facet.childsDisplayLimit || childFacetsListSize) + 100000;
	let newData = {
		random: Math.random(),
		showAllChildrenFor: [...data.showAllChildrenFor, facet._facetId]
	}
	_updateState(updateState, 'data.random', newData.random, true);
	_updateState(updateState, 'data.showAllChildrenFor', newData.showAllChildrenFor);
};

const actionHandlers = {
	[ACTIONS.FACET_DATA_FETCH_REQUESTED]: onRequest,
	[ACTIONS.FACET_DATA_FETCH_INITIATED]: fetchFacets,
	[ACTIONS.FACET_DATA_FETCH_FAILED]: onFacetFetchFail,
	[ACTIONS.FACET_DATA_FETCH_SUCCEEDED]: onFacetFetch,
	[ACTIONS.FACET_SELECTED]: onFacetSelection,
	[ACTIONS.FACET_GROUP_SELECTED]: onFacetGroupSelection,
	[ACTIONS.FACET_GROUP_UNSELECTED]: onFacetGroupDeselection,
	[ACTIONS.FACET_SHOW_MORE_REQUESTED]: onShowMoreRequest
};

export default actionHandlers;

