import {
	SUBSCRIBE_AGENT_PRESENCE,
	SUBSCRIBE_AGENT_CHANNEL_AVAIABILITY,
	SUBSCRIBE_PRESENCE_STATE,
	SUBSCRIBE_SERVICE_CHANNEL,
	SUBSCRIBE_GROUP_MEMBER,
	SUBSCRIBE_WORK_ITEM_REJECT
} from './constants';

export function subscribeChannels(dispatch, subscribe, currentUser) {
	dispatch(SUBSCRIBE_AGENT_PRESENCE, {
		filter: btoa('agent=' + currentUser).replace(/=/g, '-'),
		subscribe
	});
	dispatch(SUBSCRIBE_AGENT_CHANNEL_AVAIABILITY, {
		filter: btoa('agent=' + currentUser).replace(/=/g, '-'),
		subscribe
	});
	dispatch(SUBSCRIBE_PRESENCE_STATE, {
		filter: btoa('active=true').replace(/=/g, '-'),
		subscribe
	});
	dispatch(SUBSCRIBE_SERVICE_CHANNEL, {
		filter: btoa('active=true').replace(/=/g, '-'),
		subscribe
	});
	dispatch(SUBSCRIBE_GROUP_MEMBER, {
		filter: btoa('user=' + currentUser).replace(/=/g, '-'),
		subscribe
	});
	dispatch(SUBSCRIBE_WORK_ITEM_REJECT, {
		filter: btoa('rejected_by=' + currentUser).replace(/=/g, '-'),
		subscribe
	})
}