import { Fragment } from '@servicenow/ui-renderer-snabbdom';

const renderGrid = (inline, rules, items, styles) => {
	return (
		<div style={styles}>
			<div className={inline ? 'grid-inline' : 'grid-block'} style={rules}>
				{items.map(renderChild)}
			</div>
		</div>
	);
};

const renderChild = ({ rules, slotName, styles = {} }) => {
	const validStyles = {
		...styles,
		height: styles.height || '100%'
	};

	return (
		<Fragment>
			{slotName ? (
				<div className="grid-item" style={rules}>
					<div style={validStyles}>
						<div className="slot-wrapper">
							<slot name={slotName} />
						</div>
					</div>
				</div>
			) : null}
		</Fragment>
	);
};

export default ({ model }) => {
	const { inline, rules, items, styles } = model;
	return renderGrid(inline, rules, items, styles);
};
