import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import style from './snFormFieldValue.scss'

const view = (state, {dispatch,updateState,updateProperties}) => {
	return (
		<div className="label-value">
			<span>{state.properties.value}</span>
		</div>
	);
};

createCustomElement('sn-form-field-value', {
	renderer: {
		type: snabbdom,
		view
	},
	properties:{
		value : {default : ''}
	},
	styles: style
});
export default view;

