import { get } from 'lodash';
export const accountContextHelper = (results) => {
	let entityData = [];
	if (!results) {
		return [];
	}

	let accountId = results.sys_id.value;
	if (accountId) {
		entityData.push({
			entity: results.sys_class_name.value,
			query: `sys_id=${accountId}`,
			value: results.name.displayValue,
			label: results.sys_class_name.displayValue
		});
	}

	let primaryContactId = get(results, 'primary_contact._reference.sys_id.value', false);
	if (primaryContactId) {
		entityData.push({
			entity: results.primary_contact._reference.sys_class_name.value,
			query: `sys_id=${primaryContactId}`,
			value: results.primary_contact._reference.name.displayValue,
			label: results.primary_contact._reference.sys_class_name.displayValue
		});
	}
	return entityData;
};

export const accountInteractionHelper = (results) => {
	if (!results) {
		return {};
	}

	let interaction = {
		interactionSysId: -1,
		direction: 'outbound'
	};
	let account = get(results, 'sys_id.value', false);
	if (account) {
		interaction.context_document = account;
		interaction.context_table = results.sys_class_name.value;
		interaction.account = account;
	}
	return interaction;
};