import {t} from 'sn-translate';
import {get} from 'lodash';
import {
    SET_STATE,
    FILTER_SUBTYPES
} from '../constants';

var modalProps = {
    value: '',
    displayValue: '',
    subtypeValue: '',
    subtypeDisplayValue: '',
    subtypeField: ''
};

const debounceSearch = (searchTerm, dispatch, updateProperties, childResults, searchResults) => {
    //Trim the leading spaces on the search term before searching
    var searchTerm = searchTerm.replace(/^\s+/g,'');
    if(searchTerm.length >= 1) {
        dispatch(SET_STATE, {searchTerm: searchTerm});
        dispatch(SET_STATE, {childResults: searchResults.filter(obj => 
                obj.displayValue.toLowerCase().indexOf(searchTerm.toLowerCase()) !== -1)
                .map(obj => ({value:obj.value, displayValue:obj.displayValue, secondary: obj.secondary}))});
    } else {
        dispatch('SN_SCRIPTED_MODAL#BUTTON_ENABLED_SET', false);
        updateProperties({ selectedCaseTypes: { value: '', displayValue: '' }});
        dispatch(SET_STATE, {childResults : searchResults});
    }
};

export const onSearchTermChange = (event, dispatch, updateProperties, childResults, searchResults, selectedEntityDisplayVal) => {
    if (event) {
        let newState = {};        
        var newSearchTerm = event.target.value.replace(/^\s+/g,'');
        if (selectedEntityDisplayVal && selectedEntityDisplayVal.length > 0) {
            newState.selectedEntityDisplayVal = '';
            dispatch('SN_SCRIPTED_MODAL#BUTTON_ENABLED_SET', false);
            updateProperties({ selectedCaseTypes: { value: '', displayValue: '' }});
        }
        debounceSearch(newSearchTerm, dispatch, updateProperties, childResults, searchResults);
        newState.searchTerm = newSearchTerm; 
        if(searchResults.length > 1) {
            newState.subtypeToggled = false;
        }
        dispatch(SET_STATE, newState);
        dispatch(FILTER_SUBTYPES);
    }    
};

const debounceSubtypeSearch = (subtypeSearchTerm, dispatch, updateProperties, subtypeResults, searchResults, caseTypeValue) => {
    var subtypeSearchTerm = subtypeSearchTerm.replace(/^\s+/g,'');
    if(subtypeSearchTerm.length >= 1) {
        dispatch(SET_STATE, {subtypeSearchTerm: subtypeSearchTerm});
        searchResults.forEach(function(item) {
            if(item.parentCase === caseTypeValue) {
                dispatch(SET_STATE, {subtypeResults: item.values.filter(function(obj) {
                    return (obj.displayValue.toLowerCase().indexOf(subtypeSearchTerm.toLowerCase()) !== -1) &&
                            (item.parentCase == caseTypeValue)
                })});
            }
        })
    } else {
        updateProperties({ selectedSubtype: { value: '', displayValue: '' }});
        searchResults.forEach(function(item) {
            if(item.parentCase === caseTypeValue) {
                dispatch(SET_STATE, {subtypeResults: item.values});
            }
        });
    }
}

export const onSubtypeSearchTermChange = (event, dispatch, updateProperties, subtypeResults, searchResults, selectedSubtypeEntityDisplayValue, value) => {
    if(event) {
        let newState = {};
        var newSearchTerm = event.target.value.replace(/^\s+/g,'');
        if(selectedSubtypeEntityDisplayValue && selectedSubtypeEntityDisplayValue.length > 0) {
            newState.selectedSubtypeEntityDisplayValue = ''
            updateProperties({ selectedSubtype: { value: '', displayValue: '' }});
        }
        debounceSubtypeSearch(newSearchTerm, dispatch, updateProperties, subtypeResults, searchResults, value);
        newState.subtypeSearchTerm = newSearchTerm;
        modalProps.subtypeValue = '';
        modalProps.subtypeDisplayValue = '';
        modalProps.subtypeField = '';
        dispatch(SET_STATE, newState);
        dispatchPropsToModal(dispatch);
    }
};

export const onSelectionChange = (selectionValue, dispatch, updateProperties, selectionDisplayValue) => {
    dispatch(SET_STATE, {
        searchTerm : selectionDisplayValue,
        selectedEntityDisplayVal : selectionDisplayValue,
        subtypeSearchTerm: '',
        selectedSubtypeEntityDisplayValue: '',
        isFocused : false
    });
    updateProperties({ selectedCaseTypes: { value: selectionValue, displayValue: selectionDisplayValue }});
    updateProperties({ selectedSubtype: { value: '', displayValue: '' }});
    modalProps = {
        value: selectionValue,
        displayValue: selectionDisplayValue,
        subtypeValue: '',
        subtypeDisplayValue: '',
        subtypeField: ''
    };
    dispatch(FILTER_SUBTYPES);
    dispatchPropsToModal(dispatch);
};

export const onSubtypeSelectionChange = (selectionValue, dispatch, updateProperties, selectionDisplayValue, subtypeField) => {
    dispatch(SET_STATE, {
        subtypeSearchTerm : selectionDisplayValue,
        selectedSubtypeEntityDisplayValue: selectionDisplayValue,
        isFocused: false,
    });
    updateProperties({ selectedSubtype: { value: selectionValue, displayValue: selectionDisplayValue }});
    modalProps = {
        value: modalProps.value,
        displayValue: modalProps.displayValue,
        subtypeValue: selectionValue,
        subtypeDisplayValue: selectionDisplayValue,
        subtypeField: subtypeField
    };
    dispatchPropsToModal(dispatch);
}

export const dispatchPropsToModal = (dispatch) => {
    dispatch('SN_SCRIPTED_MODAL#BUTTON_ENABLED_SET', true);
    dispatch('SN_SCRIPTED_MODAL#DATA_SET', modalProps);
};

export const dispatchPreSelectedPropsToModal = (value, dispatch, displayValue) => {
    dispatch('SN_SCRIPTED_MODAL#BUTTON_ENABLED_SET', true);
    modalProps.value = value;
    modalProps.displayValue = displayValue;
    dispatch('SN_SCRIPTED_MODAL#DATA_SET', modalProps);
}
