import {
	NOW_BUTTON_CLICKED,
	REPORT_CONFIG_TYPE,
	REPORT_GROUP_CONFIG_TYPE
} from '../constants';
import {safelyGetValue} from '../utils/context';
import cuid from 'cuid';

export const reportActionHandlers = {
	[NOW_BUTTON_CLICKED]: ({
		action,
		state: {
			layout: { cards_info },
		},
		updateState,
	}) => {
		const { payload } = action;
		updateState({
			path: 'layout.cards_info',
			value: cards_info.map((card) =>
				card.id == payload.id && card.type == REPORT_CONFIG_TYPE
					? { ...card, vizKey: cuid() }
					: card.type == REPORT_GROUP_CONFIG_TYPE &&
					  safelyGetValue(['reportGroupConfigurations'], card).find(
					  	(report) => report.id == payload.id
					  )
						? {
							...card,
							reportGroupConfigurations: safelyGetValue(
								['reportGroupConfigurations'],
								card
							).map((report) =>
								report.id == payload.id ? { ...report, vizKey: cuid() } : report
							),
					  }
						: card
			),
			operation: 'merge',
		});
		action.stopPropagation();
	},
};
