import { t } from 'sn-translate';

const textboxes = (state, { dispatch, updateState }) => {
	return <div>
		{
			state.properties.notesheader.map((object, i) => {
				let rowsl = 12;
				let notesClass = 'notes-header';
				if (object.mandatory) {
					notesClass = 'mandatory';
				}
				let textareaId = 'notes';
				if (!object.customerVisible) {
					textareaId = 'notesInternal';
				}
				switch (object.size) {
					case 'small':
						rowsl = 6
						break
					case 'medium':
						rowsl = 12
						break
					case 'large':
						rowsl = 18
						break;
				}
				let required = object.mandatory ? "true" : false;
				return <div>
					<br id={(state.properties.description || i > 0) ? 'sn-breakline' : 'sn-hide-breakline'}></br>
					<div id={notesClass}> {t(object.heading)}</div>
					<br />
					<textarea required={required} class="sn-textarea" aria-label="textarea" id={textareaId} placeholder={t(object.placeholder)} rows={rowsl} on-input={(event) => {
						dispatch('DEBOUNCE_TEXTING', { name: object.fieldName, value: event.target.value })
					}}></textarea>
				</div>;
			})
		}</div>;
}

const view = (state, { dispatch, updateState }) => (
	<div>
		<div id={state.properties.description ? 'description' : null}>{t(state.properties.description)}</div>
		{textboxes(state, { dispatch, updateState })}
	</div>
);

export default view;