import { get } from 'lodash';

export const caseContextHelper = (results) => {
	if (!results) {
		return [];
	}
	let entityData = [];

	let caseId = get(results, 'sys_id.value', false);
	if (caseId) {
		let caseEntity = {
			entity: results.sys_class_name.value,
			query: `sys_id={results.sys_id.value}`,
			value: results.number.displayValue,
			label: results.sys_class_name.displayValue
		};
		entityData.push(caseEntity);
	}

	let accountId = get(results, 'account._reference.sys_id.value', false);
	if (accountId) {
		let acc = results.account._reference;
		entityData.push({
			entity: 'customer_account',
			query: `sys_id=${acc.sys_id.value}`,
			value: acc.name.value,
			label: 'Account'
		});
	}

	let contactId = get(results, 'contact._reference.sys_id.value', false);
	if (contactId) {
		let contact = results.contact._reference;
		entityData.push({
			entity: 'customer_contact',
			query: `sys_id=${contact.sys_id.value}`,
			value: contact.name.displayValue,
			label: 'Contact'
		});
	}

	let consumerId = get(results, 'consumer._reference.sys_id.value', false);
	if (consumerId) {
		let consumer = results.consumer._reference;
		entityData.push({
			entity: 'csm_consumer',
			query: `sys_id=${consumer.sys_id.value}`,
			value: consumer.name.value,
			label: 'Consumer'
		});
	}
	return entityData;
};

export let caseInteractionHelper = (results) => {
	if (!results) {
		return {};
	}
	let interaction = {
		interactionSysId: -1,
		direction: 'outbound'
	};

	let customerCase = get(results, 'sys_id.value', false);
	if (customerCase) {
		interaction.context_document = customerCase;
		interaction.context_table = results.sys_class_name.value;
	}

	let account = get(results, 'account._reference.sys_id.value', false);
	if (account) {
		interaction.account = account;
	}

	let contact = get(results, 'contact._reference.sys_id.value', false);
	if (contact) {
		interaction.contact = contact;
	}

	let consumer = get(results, 'consumer._reference.sys_id.value', false);
	if (consumer) {
		interaction.contact = consumer;
	}

	return interaction;

};
