import '@servicenow/now-card';
import '@servicenow/now-icon';
import '@servicenow/now-button';
import moment from 'moment';
import '@servicenow/now-empty-state';
import '@servicenow/now-loader';
import {ACTIONS, FACET_CONSTANTS} from '../../constants';
import { t } from 'sn-translate';
import '@servicenow/now-text-link';

const getFeedDateHTML = (feedDateMomentObj, todayMomentObject, dateFormat) => {
	let feedDate = '';
	let todayCopy = todayMomentObject.clone();
	if(feedDateMomentObj.isSame(
		todayMomentObject, 'day')
	){
		feedDate = 'Today';
	}
	else if(feedDateMomentObj.isSame(
		todayCopy.subtract(1, 'days'), 'day'
	)){
		feedDate = 'Yesterday';
	}
	else{
		feedDate = feedDateMomentObj.format(dateFormat);
	}
	return <div className="feed-card-date">{t(feedDate)}</div>;
};

const renderSubHeaderFields = (subHeaderField,
								 properties,
								 subobject_table_name,
								 subobject_table_label,
								 subobject_sys_id,
								 timeZoneOffset
							  ) => {
	if(!subHeaderField.value || subHeaderField.value == ''){
		return (null);
	}

	let subHeaderHTML = [];
	if(subHeaderField.show_label == 'true' && subHeaderField.label){
		subHeaderHTML.push(subHeaderField.label+': ');
	}
	if(subHeaderField.deeplink_to_subobject == 'true'){
		let buttonLabel = subHeaderField.value;
		if(subHeaderField.display_as_timeago == 'true' && (subHeaderField.type == 'glide_date_time' ||
		subHeaderField.type == 'glide_date' ||
		subHeaderField.type == 'glide_time')){
			buttonLabel = t(moment.utc(subHeaderField.value).fromNow());
		}
		subHeaderHTML.push(
			<now-button-bare 
              	configAria={{"aria-label" : subobject_table_label}}
				hide-padding
				label={buttonLabel}
				size="sm" 
				variant="primary"
				append-to-payload={
					{
						table: subobject_table_name,
						sys_id: subobject_sys_id
					}
				}
			/>
		);
	}
	else if(subHeaderField.type == 'glide_date_time' || 
			subHeaderField.type == 'glide_date' ||
			subHeaderField.type == 'glide_time'){
		if(subHeaderField.display_as_timeago == 'true')
			subHeaderHTML.push(
				t(moment.utc(subHeaderField.value).fromNow())
			);
		else
			subHeaderHTML.push(
				t(moment.utc(subHeaderField.value).utcOffset(
					timeZoneOffset).format(properties.dateFormat))
			);
	}
	else if(subHeaderField.type == 'email'){
		subHeaderHTML.push(<now-text-link
			href={'mailto:'+subHeaderField.value}
			label={subHeaderField.value}
		></now-text-link>);
	}
	else if(subHeaderField.type == 'ph_number'){
		subHeaderHTML.push(<now-text-link
			href={'tel:'+subHeaderField.value}
			label={subHeaderField.value}
		></now-text-link>);
	}
	else
		subHeaderHTML.push(
			<span>
				{	
					subHeaderField.value
				}
			</span>);
	return <span className="feed-card-subheader-field">{subHeaderHTML}</span>;
};


const renderFeedBody = (contentFields, feedItemIndex, contentFieldOverFlowObj, updateState) => {
	if(!contentFields || contentFields.length == 0){
		return  null;
	}
	let feedItemPath = 'data.feed.activities['+feedItemIndex+']';
	let contentFieldsEmpty = true;
	let feedBodyClassName = 'feed-card-body-main-content';
	if(contentFieldOverFlowObj && contentFieldOverFlowObj.showTruncated){
		feedBodyClassName += ' truncated';
	}
	let feedBodyHTML = ( <div className={feedBodyClassName}
		ref={element => {
			if(contentFieldOverFlowObj){
				return;
			}
			if(element.clientHeight == 0){
				updateState({
					path: 'kickStartElement',
					value: 0,
					operation: 'push'
				});
				return;
			}
			//since line height is 14px, 2 lines height will be 28px and giving 2px for gap between lines
			//Firefox has a bug related to line height defaulting to 15.5 so checking if scroll height is greater than 31 to show see more.
			if(element.scrollHeight > 31){
				updateState({
					path: feedItemPath+'.contentFieldOverFlowObj',
					value: {'ignoreShowMore': false, 'showTruncated': true},
					operation: 'set'
				});
			}
			else{
				updateState({
					path: feedItemPath+'.contentFieldOverFlowObj',
					value: {'ignoreShowMore': true},
					operation: 'set'
				});
			}
		}}>
		{
			contentFields.map((contentField) => {
				if(contentField.value != ''){
					contentFieldsEmpty = false;
				}
				if(contentField.type == 'translated_html'){
					return <div innerHTML={contentField.value}></div>;
				}
				return t(contentField.value);
			})
		}
	</div>);
	if(contentFieldsEmpty){
		return null;
	}
	if(contentFieldOverFlowObj){
		if(!contentFieldOverFlowObj.ignoreShowMore){
			let labelValue = 'See less';
			if(contentFieldOverFlowObj.showTruncated){
				labelValue = 'See more';
			}
			feedBodyHTML = [feedBodyHTML];
			feedBodyHTML.push(<now-button-bare hide-padding
				label={t(labelValue)}
				on-click={() => {
					updateState({
						path: feedItemPath+'.contentFieldOverFlowObj.showTruncated',
						value: (!contentFieldOverFlowObj.showTruncated),
						operation: 'set'					
					});
				}} 
				size="sm" 
				variant="primary">
			</now-button-bare>);
		}
	}
	return (contentFieldsEmpty)? null: feedBodyHTML;
};

const getFeedItemHTML = (feedItem, feedItemIndex, properties, timeZoneOffset, helpers) => {
	let {icon, title, subheader_fields, content_fields, subobject_table_name,subobject_table_label, subobject_sys_id, contentFieldOverFlowObj} = feedItem;
	let {updateState} = helpers;
	let noFeedIcon = properties.hideFeedIcons || (!icon);
	return (<now-card	className="feed-card" 
		interaction="none" 
		size="lg"
	>
		<div className="feed-card-header">
			{
				!noFeedIcon? <now-icon 
					icon={icon} 
					size="md" 
					className="feed-card-icon"
				/> : <div className="feed-card-no-icon"></div>
			}
			<span>
				{
					title
				}
			</span>
		</div>
		<div className={noFeedIcon? 'feed-card-body': 'feed-card-body has-feed-icon'}>
			<div className="feed-card-subheader">
				{
					subheader_fields.map(
						(subHeaderField) => {
							let subHeaderFieldHTML = renderSubHeaderFields(
								subHeaderField, 
								properties,
								subobject_table_name,
								subobject_table_label,
								subobject_sys_id,
								timeZoneOffset
							);
							return subHeaderFieldHTML;
						})
				}
			</div>
			{
				(content_fields && 
					content_fields.length != 0) ? 
					renderFeedBody(
						content_fields,
						feedItemIndex,
						contentFieldOverFlowObj,
						updateState
					) : 
					null
			}
		</div>
	</now-card>);
};

const getEmptyFeedHTML = (selectedFacet)=>{	
	let headerText = 'No activity found for any facets.';
	let bodyText = 'Try changing the date range.';
	if(selectedFacet != FACET_CONSTANTS.ALL_ACTIVITY_HEAD){
		headerText = 'No activity found for this facet.';
		bodyText = 'Try changing the date range, or selecting a different facet.';
	}
	return <now-empty-state 
		type="empty" 
		variant="fit" 
		icon={null}
		headerText={t(headerText)}
		bodyText={t(bodyText)}>
	</now-empty-state>;
};

const getFeedLoadingHTML = (isShowMore)=>{
	if(isShowMore)
		return	<now-loader 
			className="feed-showmore-loader" 
			size="lg"
		/>;
	return <now-loader 
		className="feed-loader" 
		size="lg"
	/>;
};

const view = (feedState, properties, helpers) => {
	let {
		feed, 
		isFeedLoading,
		isFeedShowMoreLoading,
		selectedFacet,
		timeZoneOffset
	} = feedState;
	if(isFeedLoading)
		return getFeedLoadingHTML();
	if(!feed)
		return getEmptyFeedHTML(selectedFacet);
	let currentFeed = feed;
	let feedActivities = currentFeed.activities;
	if(!feedActivities || feedActivities.length == 0)
		return getEmptyFeedHTML(selectedFacet);
	let todayMomentObject = moment.utc().utcOffset(timeZoneOffset);
	let feedDateMomentObj = moment.utc(feedActivities[0].created).utcOffset(timeZoneOffset);
	let feedHTML = [];
	feedHTML.push(getFeedDateHTML(
		feedDateMomentObj, 
		todayMomentObject, 
		properties.dateFormat
	));
	feedActivities.map((feedItem, feedItemIndex) => {
		let feedItemHTML = getFeedItemHTML(feedItem, feedItemIndex, properties, timeZoneOffset, helpers);
		let feedItemMomentObj = moment.utc(feedItem.created).utcOffset(timeZoneOffset);
		if(!feedDateMomentObj.isSame(feedItemMomentObj, 'day')){
			feedHTML.pop();
			feedDateMomentObj = feedItemMomentObj;
			feedHTML.push(getFeedDateHTML(
				feedDateMomentObj, 
				todayMomentObject, 
				properties.dateFormat
			));
		}
		feedHTML.push(feedItemHTML);
		if(!properties.hideTimeLeaps) 
			feedHTML.push(
				<div 
					className="feed-card-vertical-line-separator">
				</div>);
		else 
			feedHTML.push(
				<div 
					className="feed-card-vertical-separator">
				</div>);
	});
	feedHTML.pop();
	if(currentFeed.hasMoreRecords){
		if(!isFeedShowMoreLoading)
			feedHTML.push(
				<now-button 
					label={t('Show more')} 
					className="show-more-button" 
					variant="primary"
					on-click={ () => {
						let payLoad = {
							'stFrom': feedActivities.length
						};
						helpers.dispatch(ACTIONS.FEED_SHOW_MORE_CLICKED, payLoad);
					}}>
				</now-button>);
		else
			feedHTML.push(getFeedLoadingHTML(true));
	}
	return <div className="feed-card-item-container">
		{feedHTML}
	</div>;
};

export default view;
