import '@servicenow/now-record-list-connected';
import '../sn-sold-product-form';
import snContentTree from 'sn-component-content-tree-with-data';
import snProductModelTree from 'sn-component-product-model-hierarchy';
import { t } from 'sn-translate';

const view = (state, dispatch) => {
    
	var accountSysId = (state.properties.model && state.properties.model.sysId)? state.properties.model.sysId : state.properties.sysId;

	return (
		<div className="container">
			<div className="split-left">
				<sn-content-tree-with-data 
					parentFieldName={state.properties.parentFieldName} 
					displayFieldName={state.properties.displayFieldName} 
					query = {'account=' + accountSysId}
					tableName = {state.properties.tableName} 
					maxChildren = {state.properties.maxChildren}
					label = {state.properties.label}> 
				</sn-content-tree-with-data>
			</div>

            
			{
				state.selectedSoldProduct?
					<div className="split-right">
						<div>
							<sn-sold-product-form sysId={state.selectedSoldProduct.id}></sn-sold-product-form>
							<hr/>
							{
								state.openModal?
									<div>
										<now-modal size="lg" header-label={t('Product Model for ')+state.selectedSoldProduct.label}
											shouldFocusOnOpen={true}
											context='form'
											opened={true}>
											<sn-product-model-hierarchy sysId={state.selectedSoldProduct.id}></sn-product-model-hierarchy>
										</now-modal>
									</div>
									:<div/>
							}
						</div>
						<div className="list-container">
							<now-record-list-connected 
								table = 'sn_install_base_m2m_installed_product'
								columns = {state.properties.listColumns}
								listTitle = {t('Install Base')}
								listType={'RELATED'}
								limit={5}
								hideQuickEdit={true}
								hideFilterPanel={true}
								hideNewButton={true}
								hideMultiEdit={true}
								hidePanelAdvanced={true}
								hideColumnFiltering={true}
								hideCellFilter={true}
								hideRowSelector={true}
								hideColumnGrouping={true}
								query = {'sold_productIN' + state.selectedSoldProduct.id + '^sold_product.account=' + accountSysId}>
							</now-record-list-connected>
						</div>
					</div>
					: 
					<div className="split-right">
						<div className="no-select">
							<div className="back-ground-image"/>
							<div className="center">
								<span className="heading">{t('No records to display')}</span>
								<span className="text">{t('Select product (if any) from the hierarchy')}</span>  
							</div>
						</div> 
					</div> 
			}
		</div>
       
	);
};
export default view;
