import buildFeed from './components/feed';
import buildFacets from './components/facets';
import '@servicenow/now-heading';
import '@servicenow/now-icon';
import '@servicenow/now-button';
import '@servicenow/now-date-range-picker';
import {t} from 'sn-translate';
import {INTERNAL_DATE_FORMAT} from './constants';

const renderDateRangePicker = (startDate, endDate) => {
	return <now-date-range-picker
		className="small-margin date-picker-container"
		start={startDate}
		end={endDate}
		format={INTERNAL_DATE_FORMAT}
	/>;
};

const renderFeed = ({data , properties, feedTitle, startDate, endDate}, helpers) => {
	return (<div className="feed-container light-border">
		<div className="feed-container-header">
			<now-heading 
				className="feed-container-title" 
				label={
					feedTitle?	
						t(feedTitle): 
						t('All activity')
				} level="4"
				variant="header-tertiary" 
				hasNoMargin={true}
				wontWrap
			/>
			<div className="feed-container-header-filters">
				{
					(properties.disableDateRangeFilter)?null:renderDateRangePicker(
						startDate, 
						endDate
					)
				}
			</div>
		</div>
		<hr className="feed-header-hr no-margin"/>
		<div className="feed-container-main-content">
			{buildFeed(data, properties, helpers)}
		</div>
	</div>);
};

const renderFacets = (
	{
		data: {facets, selectedFacet, expandedFacet, loadingFacets},
		properties, filters
	}, helpers) => {
	return <div className="facet-container light-border">
		{buildFacets({
			facets,
			filters,
			selectedFacet,
			expandedFacet,
			loadingFacets,
			properties
		}, helpers)}
	</div>;
};

const view = (state, helpers) => {
	const {properties: {disableFacets}} = state;
	return (
		<div className="component-container">
			{disableFacets ? '' : renderFacets(state, helpers)}
			{renderFeed(state, helpers)}
		</div>);
};

export default view;
