import { get } from 'lodash';
import {
	getCurrentStateIconClass,
	getPsData,
	getName,
	getIconStatus,
	getStateSysId,
	getAgentStateId
} from './selectors';
import {
	OFFLINE_SYS_ID,
	AVAILABLE_STATE,
	PARTIALLY_AVAILABLE_STATE,
	OFFLINE_STATE,
	AWAY_STATE,
	REJECTION_CONTEXT
} from './constants'

function parseStateIcon(presenceStates) {
	const stateIconType = {};
	presenceStates.map(presenceState => {
		presenceState.agent_channel_availability.sort((a, b) => a.name > b.name);
		if(presenceState.sys_id === OFFLINE_SYS_ID) {
			stateIconType[presenceState.sys_id] = getCurrentStateIconClass(OFFLINE_STATE);
		} else if(presenceState.available_to_receive_work_items === false) {
			stateIconType[presenceState.sys_id] = getCurrentStateIconClass(AWAY_STATE);
		} else {
			stateIconType[presenceState.sys_id] = isFullyAvailable(presenceState.agent_channel_availability) ?
				getCurrentStateIconClass(AVAILABLE_STATE) : getCurrentStateIconClass(PARTIALLY_AVAILABLE_STATE);
		}
	});
	return {presenceStates, stateIconType};
}

function isFullyAvailable(agentChannelAvailability) {
	return agentChannelAvailability.every( availability => availability.available );
}

export function extractPresenceDataFromPayload(payload) {
	const psData = getPsData(payload);
	if (!psData) {
		return null;
	}

	const {agent_presence, presence_state} = psData;
	const presenceStatesData = parseStateIcon(presence_state);
	const currentState = getName(agent_presence);
	const currentIconStatus = getIconStatus(agent_presence);
	const currentStateSysId = getStateSysId(agent_presence);
	const currentAgentStateId = getAgentStateId(agent_presence);
	if (currentState === null || currentIconStatus === null || presenceStatesData === null || currentStateSysId === null || currentAgentStateId === null) {
		return null;
	}
	return {presenceStatesData, currentState, currentIconStatus, currentStateSysId, currentAgentStateId};
}

export function getPresencePayload(presenceData) {
	const psData = getPsData(presenceData);
	if (!psData) {
		return null;
	}
	const {agent_presence, presence_state} = psData;
	const presenceStateSysID = get(agent_presence[0],'current_presence_state_id','');
	const presence = {
		name: getName(agent_presence),
		sys_id: getStateSysId(agent_presence),
		available: false,
		channels: []
	};
	const presenceStatesData = parseStateIcon(presence_state);
	presence.available = get(presenceStatesData,'presenceStates',[]).some(presenceState => {
			return (presenceState.name === presence.name) && presenceState.available_to_receive_work_items;
		}
	);
	const channels = get(presenceStatesData,'presenceStates',[])
		.filter(presenceStates => {
			presenceStateSysID
			return (presenceStates.sys_id === presenceStateSysID) && (presenceStates.agent_channel_availability.length > 0);
		});
	if (!channels.length) {
		presence.channels = channels;
	} else {
		presence.channels = channels[0].agent_channel_availability.map(channel => {
			const channelObj = {
				name: channel.name,
				available: channel.available,
				sys_id: channel.service_channel_id,
				restrict_update: channel.restrict_update
			};
			return channelObj;
		});
	}
	return presence;
}

export function getCurrentUserId(behaviors){
	return get(behaviors,'wsUserData.sys_id');
}

export function dispatchWorkItems(dispatch, workItems, eventMap) {
	workItems.forEach(element => {
		// if rejection item
		if(element.isRejectionEvent){
			dispatch(eventMap.OPENFRAME_AWA_WORKITEM_REJECTED, { workItem : element.workItem });
		} else {
			// not a rejection item, check if it is a work item offered or work item accepted item
			if(!element.isRejectionEvent && element.workItemState ){
				switch(element.workItemState.toLowerCase()){
					case 'pending_accept':
						dispatch(eventMap.OPENFRAME_AWA_WORKITEM_OFFERED, { workItem : element.workItem });
						break;
					case 'accepted':
						dispatch(eventMap.OPENFRAME_AWA_WORKITEM_ACCEPTED, { workItem : element.workItem });
						break;
				}
			}
		}
	});
}

export function extractInteractionData(interactionData) {
	let interactionPayload = {
		sys_id: get(interactionData,'sys_id'),
		number: get(interactionData,'number'),
		type: get(interactionData,'type'),
		reason: get(interactionData,'reason')
	}
	// pass the subtype if it has a non null value
	if(get(interactionData,'subtype')) {
		interactionPayload.subtype = get(interactionData, 'subtype');
	}
	return interactionPayload;
}

export function extractRejectedWorkItem(data){
	if(!data){
		return [];
	}
	let payload = (get(data.payload.data.GlideRecord_Query.awa_work_item_rejection, '_results',[]));
	let returnValue = [];
	payload.forEach(element => {
		let queryReason = get(element.reason,'_reference', {});
		let queryWorkItem = (element._query.GlideRecord_Query.awa_work_item._results 
			&& element._query.GlideRecord_Query.awa_work_item._results.length) ? element._query.GlideRecord_Query.awa_work_item._results[0] : {};
		let record  = {
			workItem: {
				sys_id: get(element.work_item, 'value', ''),
				size: get(queryWorkItem.size, 'value', null),
				state: get(queryWorkItem.state, 'value', null)
			},
			serviceChannel:{
				name: get(queryWorkItem.queue._reference.service_channel._reference.name, 'value', ''),
				sysID: get(queryWorkItem.queue._reference.service_channel._reference.sys_id, 'value', '')
			},
			document:{
				document_sys_id: get(queryWorkItem.document_id, 'value', ''),
				document_table: get(queryWorkItem.document_table, 'value', '')
			},
			rejection:{
				sys_id: get(queryReason.sys_id, 'value', ''),
				reason: get(queryReason.reason, 'value', '')
			}
		}
		returnValue.push(record);
	});
	return returnValue;
}
