import '@servicenow/now-card';
import '@servicenow/now-heading';
import '@devsnc/sn-layout';

import { renderVisual } from './report';

import { getSlotNameFn, generateModel } from '../../utils/modelGenerator';
import {GROUP_INTERNAL_SPLIT} from '../../constants';

const getSlotName = getSlotNameFn(GROUP_INTERNAL_SPLIT);

const renderVisualGroup = ({ id, slotName, visualGroupJSON: {title, reportGroupConfigurations}}) => (
	<now-card key={id} className="sn-customer-information-report-group" interaction="none" size="lg" slot={slotName}>
		<now-heading
			label={title}
			level="2"
			variant="header-secondary"
			wontWrap
		/>
		<sn-layout model={generateModel(GROUP_INTERNAL_SPLIT)}>
			{reportGroupConfigurations.map((report, index) =>
				renderVisual({
					slotName: getSlotName(index),
					visualJSON: report,
					variant : 'SMALL',
					mainTitle: false
				})
			)}
		</sn-layout>
	</now-card>
);

export { renderVisualGroup };
