import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-highlighted-value';

import moment from 'moment-timezone';
/**
 * A timer component for customer information
 * 
 * ```
 * <sn-customer-information-time-tag />
 * ``` 
 * @seismicElement sn-customer-information-timer
 * @summary A timer component for customer information
 * 
 */
createCustomElement('sn-customer-information-time-tag', {
	renderer: { type: snabbdom },
	initialState: {
		time: '',
	},
	properties: {
		timeZone: {
			default: '',
			schema : {type : 'string'}
		},
		timeFormat: {
			default: 'LT z',
			schema : {type : 'string'}
		},
	},
	view: ({ time }) => {
		return <now-highlighted-value label={time} />;
	},
	transformState(state) {
		const {
			properties: { timeZone, timeFormat },
		} = state;
		return { ...state, time: moment().tz(timeZone).format(timeFormat) };
	},

	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			updateState,
			properties: { timeZone, timeFormat },
		}) => {
			setInterval(
				() =>
					updateState(() => ({
						path: 'time',
						value: moment().tz(timeZone).format(timeFormat),
						operation: 'set',
						shouldUpdate: true,
					})),
				1000
			);
		},
	},
});
