import clone from 'lodash/clone';
import get from 'lodash/get';
import isEqual from 'lodash/isEqual';
import isPlainObject from 'lodash/isPlainObject';
import setWith from 'lodash/setWith';

export const unwrapPath = (path) => (isPlainObject(path) ? path.path : path);
export const hasPath = (list, path) =>
  list.findIndex((p) => isEqual(unwrapPath(p), unwrapPath(path))) > -1;
export const removePath = (list, path) =>
	list.filter((p) => !isEqual(unwrapPath(p), unwrapPath(path)));
export const addPath = (list, path) => [...list, path];
export const togglePath = (list, path) =>
  hasPath(list, path) ? removePath(list, path) : addPath(list, path);

export const toLodashPath = (items, path) => {
  let ids = [...path];
  let next = items;
  let result = [];
  while (ids.length) {
    const id = ids.shift();
    const index = next.findIndex((item) => item.id === id);
    if (ids.length === 0) {
      return [...result, index];
    }
    result = [...result, index, 'children'];
    next = next[index].children;
  }
  return undefined;
};

export const setItem = (items, path, fn) => {
  const lodashPath = toLodashPath(items, path);
  const item = get(items, lodashPath);
  return setWith(clone(items), lodashPath, clone(fn(item)), clone);
};

// Update the tree based on the path and the given children
export const setItemChildren = (items, path, children) =>
  setItem(items, path, (item) => ({
    ...item,
    children: [...(item.children || []), ...children]
  }));

// The Tree Control Event may return a partial path. Walk the tree
// and return the full path
export const getFullUpdatePath = (fullTree, partialPath) => {
  let returnPath = partialPath.slice(0);
  const firstNode = returnPath[0];

  let currentChild = findChildParentPath(fullTree, firstNode);

  if (currentChild != null) {
    currentChild.push.apply(currentChild, returnPath);
    return currentChild;
  } else {
    return returnPath;
  }
}

function findChildParentPath(tree, childId) {
  let currentItem = tree[0];
  
  for (let i = 0; i < currentItem.children.length; i++) {
    if (currentItem.children[i].id === childId) {
      return [currentItem.id];
    } else {
      if (currentItem.children[i].children.length == 0) continue;

      let foundChildParentPath = findChildParentPath([currentItem.children[i]], childId);
      if (foundChildParentPath != null) {
        foundChildParentPath.unshift(currentItem.id);
        return foundChildParentPath;
      }
    }
  }

  return null;
}
  