import {ACTIONS, FACET_CONSTANTS, FEED_CONSTANTS, APIS, CONSTANTS, INTERNAL_DATE_FORMAT, INTERNAL_DATE_TIME_FORMAT} from './constants';
import {actionTypes} from '@servicenow/ui-core';
import facetActionHandlers from './components/facets/actionHandlers';
import feedActionHandlers from './components/feed/actionHandlers';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import moment from 'moment';
import {getStartAndEndInUTC} from './utils/feedUtils';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {PROPERTY_QUERY} from './query'

const fetchProperty = createGraphQLEffect(PROPERTY_QUERY, {
	variableList: ['name'],
	successActionType: ACTIONS.PROPERTY_FETCH_SUCCEEDED,
	errorActionType: ACTIONS.PROPERTY_FETCH_FAILED
});

const onPropertyFetchSuccess = ({action: { payload }, state, updateState, dispatch}) => {
	let dateFilterInitialOffset = 30;
	if(payload && payload.data &&
		payload.data.GlideDomain_Query &&
		payload.data.GlideDomain_Query.property &&
		payload.data.GlideDomain_Query.property.isDefined){
		dateFilterInitialOffset = Math.max(payload.data.GlideDomain_Query.property.intValue, 0);
	}
	updateDateFilterOffset(dateFilterInitialOffset, state, updateState, dispatch);
};

const onPropertyFetchFailure = ({state, updateState, dispatch}) => {
	console.error(t("Property fetch failed."));
	let dateFilterInitialOffset = 30;
	updateDateFilterOffset(dateFilterInitialOffset, state, updateState, dispatch);
};

const updateDateFilterOffset = (dateFilterInitialOffset, state, updateState, dispatch) => {
	updateState({
		path:'dateFilterInitialOffset',
		value: dateFilterInitialOffset,
		operation: 'set',
		shouldRender: false
	});
	let {
		start: startDateInUTC,
		end: endDateInUTC
	} = updateStartAndEndDates(
			state.data.timeZoneOffset,
			dateFilterInitialOffset,
			updateState
		);
	let {contextId, context, activitiesListSize} = state.properties;
	dispatch(ACTIONS.FACET_DATA_FETCH_REQUESTED, {
		'context_instance': contextId,
		'context': context,
		'start_date': startDateInUTC,
		'end_date': endDateInUTC,
		'before': state.before
	});
	dispatch(ACTIONS.FEED_DATA_FETCH_REQUESTED, {
		'context_instance': contextId,
		'context': context,
		'stFrom': 0,
		'last': activitiesListSize,
		'start_date': startDateInUTC,
		'end_date': endDateInUTC,
		'before': state.before
	}, {
		'facets': '',
		'record_id': ''
	});
};

const setUserTimeZone = (state, updateState) => {
	let {wsUserData} = state.behaviors;
	let timeZoneOffset = 0;
	if(wsUserData &&
		wsUserData.timeZoneOffset){
		timeZoneOffset = (wsUserData.timeZoneOffset/60000);
	}
	updateState({
		path: 'data.timeZoneOffset',
		value: timeZoneOffset,
		operation: 'set',
		shouldRender: false
	});
};

const updateStartAndEndDates = (timeZoneOffset, dateFilterOffset, updateState) => {
	let startDate = moment.utc().utcOffset(timeZoneOffset).subtract(
		dateFilterOffset, 'days'
	).format(INTERNAL_DATE_FORMAT);
	let endDate = moment.utc().utcOffset(timeZoneOffset).format(INTERNAL_DATE_FORMAT);
	let {start, end} = getStartAndEndInUTC(startDate, endDate, timeZoneOffset);
	updateState({
		path: 'startDate',
		value: startDate,
		operation: 'set',
		shouldRender: false
	});
	updateState({
		path: 'endDate',
		value: endDate,
		operation: 'set',
		shouldRender: false
	});
	updateState({
		path: 'startDateInUTC',
		value: start,
		operation: 'set',
		shouldRender: false
	});
	updateState({
		path: 'endDateInUTC',
		value: end,
		operation: 'set',
		shouldRender: true
	});

	return {
		startDate, endDate, start, end
	};
}

const onBootstrap = ({properties: {activityModule, fields}, dispatch, updateProperties, updateState, state}) => {

	//fetching data required form page presources
	setUserTimeZone(state, updateState);

	//setting before parameter
	updateState({
		path: 'before',
		value: moment.utc().format(INTERNAL_DATE_TIME_FORMAT),
		operation: 'set',
		shouldRender: false
	});

	// For Paris, Hardcoded context identification from parent record.
	// Works with specific column names only - contact and consumer.
	let name = '';
	let contextId = '';
	if (fields && fields.consumer && fields.consumer.value && fields.consumer.value != ''){
		name = CONSTANTS.TABLE_CONSUMER;
		contextId = fields.consumer.value;
	}
	else if (fields && fields.contact && fields.contact.value && fields.contact.value != '') {
		name = CONSTANTS.TABLE_CONTACT;
		contextId = fields.contact.value;
	}
	dispatch(ACTIONS.CONTEXT_DATA_FETCH_REQUESTED, {'activity_module': activityModule, name});
	updateProperties({contextId});
};

const onContextFetchRequested = createHttpEffect(APIS.CONTEXTS_API, {
	method: 'GET',
	queryParams: ['activity_module', 'name'],
	successActionType: ACTIONS.CONTEXT_DATA_FETCH_SUCCEEDED,
	errorActionType: ACTIONS.CONTEXT_DATA_FETCH_FAILED
});

const onContextFetch = (
	{
		state,
		action: {
			payload: {result: result}
		},
		dispatch, updateProperties
	}) => {
	if(!result || result.length == 0) {
		console.error('No activity context found for selected record');
		return;
	}
	const activityContext = result[0];
	const {sys_id: context} = activityContext;
	updateProperties({'context': context});
	dispatch(ACTIONS.PROPERTY_FETCH_INITIATED, {
		name: CONSTANTS.DATE_FILTER_INITIAL_OFFSET_PROPERTY_NAME
	});
};

const onContextFetchFail = ({state:{data}, updateState}) => {
	updateState({
		path: 'data',
		value: {...data,
			isFeedLoading : false,
			loadingFacets: false
		},
		operation: 'set'
	});
	console.error('Activity context fetch failed.');
};

const onDateRangeChange = ({action,
	state,
	updateState,
	properties: {context, contextId: context_instance, activitiesListSize},
	dispatch
}) => {
	let {timeZoneOffset} = state.data;
	if(action && action.payload && action.payload.start && action.payload.end){
		let {start, end} = getStartAndEndInUTC(action.payload.start, action.payload.end, timeZoneOffset);
		let before = moment.utc().format(INTERNAL_DATE_TIME_FORMAT);
		updateState({
			...state,
			startDate: action.payload.start,
			endDate: action.payload.end,
			startDateInUTC: start,
			endDateInUTC: end,
			before: before,
			data: {
				...state.data,
				feedInfo: {
					[FACET_CONSTANTS.ALL_ACTIVITY_HEAD]: {
						[FEED_CONSTANTS.FEED_HEAD]: {
							activitiesCount: 0
						}
					}
				},
				feed: {
					activities: [],
					hasMoreRecords: false
				}
			}
		});
		dispatch(ACTIONS.FEED_DATA_FETCH_REQUESTED, {
			'context_instance': context_instance,
			'context': context,
			'stFrom': 0,
			'last': activitiesListSize,
			'start_date': start,
			'end_date': end,
			'facets': state.data.selectedFacets? state.data.selectedFacets : '',
			'record_id': state.data.selectedRecordId? state.data.selectedRecordId: '',
			'before': before
		}, {
			'facets': state.data.selectedFacets? state.data.selectedFacets : '',
			'record_id': state.data.selectedRecordId? state.data.selectedRecordId: ''
		});

		dispatch(ACTIONS.FACET_DATA_FETCH_REQUESTED, {
			'context_instance': context_instance,
			'context': context,
			'start_date': start,
			'end_date': end,
			'before': before,
			'feedFilterApplied': true
		});
	}
};


const myHandlers = {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: onBootstrap,
	[ACTIONS.CONTEXT_DATA_FETCH_REQUESTED]: onContextFetchRequested,
	[ACTIONS.CONTEXT_DATA_FETCH_SUCCEEDED]: onContextFetch,
	[ACTIONS.CONTEXT_DATA_FETCH_FAILED]: onContextFetchFail,
	[ACTIONS.DATE_RANGE_PICKER_VALUE_CHANGED]: onDateRangeChange,
	[ACTIONS.PROPERTY_FETCH_INITIATED]: fetchProperty,
	[ACTIONS.PROPERTY_FETCH_SUCCEEDED]: onPropertyFetchSuccess,
	[ACTIONS.PROPERTY_FETCH_FAILED]:onPropertyFetchFailure
};


const actionHandlers = {
	...facetActionHandlers,
	...feedActionHandlers,
	...myHandlers
};

export default actionHandlers;
