export const openFrameConfigQuery_read = `query {
    AppOpenframe_Query {
      showFrame
      config {
        title
        subTitle
        configuration
        height
        width
        navIconClass
        titleIcon
        collapsedViewEnabled
        iframeAllow
        showPresenceIndicator
        url {
          authority
          fragment
          host
          path
          port
          query
          scheme
          schemeSpecificPart
          userInfo
          isAbsolute
          isOpaque
          stringValue
        }
      }
    }
  }`;

export const contactDetailsQuery_read = `query ($sys_id: String) {
    GlideRecord_Query {
      customer_contact(sys_id: $sys_id) {
        _results {
          name {
            displayValue
          }
          sys_class_name {
            displayValue
            value
          }
          sys_id{
            value
          }
          account {
            _reference {
              sys_class_name {
                displayValue
                value
              }
              sys_id{
                value
              }
              name {
                displayValue
              }
  
            }
          }
        }
      }
    }
  }
  `;
  
