import { createHttpEffect } from '@servicenow/ui-effect-http';
import { actionTypes } from '@servicenow/ui-core';

import { safelyGetValue, getCurrentContext } from '../utils/context';
import { generateId } from '../utils';
import cuid from 'cuid';
import 'intersection-observer';

import {recordActionHandlers} from './record.actions';
import {reportActionHandlers} from './report.actions';

import {
	CONFIG_INFO_FETCHED,
	CONFIG_INFO_FETCH_SUCCEEDED,
	CONFIG_INFO_FETCH_FAILED,
	CUST_INFO_CONFIG_API,
	FIELD_CONSUMER,
	FIELD_CONTACT,
	TABLE_CONSUMER,
	TABLE_CONTACT,
	REPORT_CONFIG_TYPE,
	REPORT_GROUP_CONFIG_TYPE,
	MULTIPLE_USER_RECORDS_FETCHED,
	COMPONENT_BOTTOM_REACHED,
} from '../constants.js';

const contextCandidates = [
	{ contextField: FIELD_CONSUMER, contextTable: TABLE_CONSUMER },
	{ contextField: FIELD_CONTACT, contextTable: TABLE_CONTACT },
];

const addRefreshKey = (cardsInfo) =>
	cardsInfo.map((card) =>
		card.type == REPORT_CONFIG_TYPE
			? { ...card, vizKey: cuid() }
			: card.type == REPORT_GROUP_CONFIG_TYPE
				? {
					...card,
					reportGroupConfigurations: safelyGetValue(
						['reportGroupConfigurations'],
						card
					).map((report) => ({
						...report,
						visKey: cuid(),
					})),
			  }
				: card
	);

const fetchConfigEffect = createHttpEffect(CUST_INFO_CONFIG_API, {
	method: 'GET',
	pathParams: ['contextTable', 'contextId', 'targetTable'],
	queryParams: ['start', 'end'],
	successActionType: CONFIG_INFO_FETCH_SUCCEEDED,
	errorActionType : CONFIG_INFO_FETCH_FAILED
});

const initiateConfigFetch = (
	fields,
	contextCandidates,
	targetTable,
	{ start, pageSize },
	{ updateProperties, dispatch }
) => {
	const { contextField, contextTable } = getCurrentContext(
		fields,
		contextCandidates
	);
	const contextId = safelyGetValue([contextField, 'value'], fields);
	dispatch(CONFIG_INFO_FETCHED, {
		contextTable,
		contextId,
		targetTable,
		start,
		end: start + pageSize,
	});
};

const onDomReadyHandler = ({ host, dispatch, cards_info }) => {
	const loader = host.shadowRoot.querySelector(
		'.sn-customer-information-loader'
	);

	const options = {
		root: null,
		rootMargin: '10%',
		threshold: 0.8,
	};
	const loadMoreCallback = (entires, observer) => {
		entires.forEach(
			(entry) => entry.isIntersecting && dispatch(COMPONENT_BOTTOM_REACHED)
		);
	};

	const observer = new IntersectionObserver(loadMoreCallback, options);

	loader && observer.observe(loader);
};

const actionHandlers = {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
		dispatch,
		updateProperties,
		state: { start, pageSize },
		properties: { contextTable, contextId, table: targetTable, fields },
	}) =>
		initiateConfigFetch(
			fields,
			[
				{ contextField: FIELD_CONSUMER, contextTable: TABLE_CONSUMER },
				{ contextField: FIELD_CONTACT, contextTable: TABLE_CONTACT },
			],
			targetTable,
			{ start, pageSize },
			{ updateProperties, dispatch }
		),

	[actionTypes.COMPONENT_DOM_READY]: ({
		host,
		dispatch,
		state: {
			layout: { cards_info },
		},
	}) =>
		onDomReadyHandler({
			host,
			dispatch,
			cards_info,
		}),

	[CONFIG_INFO_FETCHED]: fetchConfigEffect,

	[CONFIG_INFO_FETCH_SUCCEEDED]: ({
		action: { payload },
		updateState,
		dispatch,
		state,
	}) => {
		updateState({
			...state,
			layout: {
				...state.layout,
				...payload.result,
				cards_info: [
					...state.layout.cards_info,
					...addRefreshKey(generateId(payload.result.cards_info)),
				],
			},
			start: state.start + state.pageSize,
			isLoading: false,
		});
		dispatch(MULTIPLE_USER_RECORDS_FETCHED);
	},

	[CONFIG_INFO_FETCH_FAILED]: ({ updateState }) =>
		updateState({
			path: 'isError',
			value: true,
			operation: 'set',
		}),

	[COMPONENT_BOTTOM_REACHED]: ({
		dispatch,
		updateProperties,
		updateState,
		state: { start, pageSize, layout },
		properties: { table: targetTable, fields },
	}) => {
		if (safelyGetValue(['has_more_records'], layout) && start) {
			updateState({
				path: 'isLoading',
				value: true,
				operation: 'set',
			});
			initiateConfigFetch(
				fields,
				contextCandidates,
				targetTable,
				{ start, pageSize },
				{ updateProperties, dispatch }
			);
		}
	},

	...recordActionHandlers,
	...reportActionHandlers,
		
};

export { actionHandlers };
