import { get } from 'lodash';
import { STATE_CSS_MAP } from './constants';

export function getPsData(presenceStateSource) {
	return get(presenceStateSource, 'data.GlideAWA_Query');
}

export function getName(agentPresence) {
	return get(agentPresence, '[0].name');
}

export function getIconStatus(agentPresence) {
	return get(agentPresence, '[0].state_icon_type');
}

export function getStateSysId(agentPresence) {
	return get(agentPresence, '[0].current_presence_state_id');
}

export function getAgentStateId(agentPresence) {
	return get(agentPresence, '[0].sys_id');
}

export function getCurrentStateIconClass(currentIconStatus) {
	return get(STATE_CSS_MAP, `${currentIconStatus}`, '');
}