import '@servicenow/now-avatar';
import '@servicenow/now-card';
import '@servicenow/now-heading';
import '@servicenow/now-label-value';
import '@servicenow/now-highlighted-value';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

import '../timer';

const renderSubHeader = ({
	title: subTitle,
	subheader_tags: subHeaderTags,
	isUser,
	timeZone,
	avatar,
}) => (
	<Fragment>
		{isUser ? (
			<now-avatar
				className="sn-customer-information-record-value-avatar"
				imageSrc={avatar}
				userName={subTitle}
			/>
		) : null}
		<div className="sn-customer-information-record-value-heading">
			{subTitle ? (
				<now-heading
					label={subTitle}
					level="2"
					variant="header-tertiary"
					wontWrap
				/>
			) : null}
			{isUser ? (
				<sn-customer-information-time-tag
					className="sn-customer-information-record-value-tag"
					timeFormat={'LT z'}
					timeZone={timeZone}
				/>
			) : null}
			{subHeaderTags
				? subHeaderTags.map(({ label, color }) => (
					<span className="sn-customer-information-record-value-tag">
						<now-highlighted-value label={label} color={color} />
					</span>
				  ))
				: null}
		</div>
	</Fragment>
);

const renderFields = (fields) => (
	<div className="sn-customer-information-record-value-label">
		<now-label-value-stacked
			align="horizontal"
			delimiter=","
			items={fields}
			size="md"
			wrapped
			truncated
		></now-label-value-stacked>
	</div>
);

const renderRecord = ({
	slotName,
	recordJSON: {
		title,
		subheader_fields: subHeaderFields,
		fields,
		isUser,
		avatar,
		timeZone,
		id,
	},
}) => (
	<now-card key={id} interaction="none" size="lg" slot={slotName}>
		<now-heading label={title} level="2" variant="header-secondary" wontWrap />
		<div className={isUser ? 'sn-customer-information-record-value' : ''}>
			{subHeaderFields
				? renderSubHeader({ ...subHeaderFields, isUser, timeZone, avatar })
				: null}
			{fields ? renderFields(fields) : null}
		</div>
	</now-card>
);
export { renderRecord };
