const event = { type: 'NOW.PostMessage' };

const frameResponse = (eventName, args) => {
	return JSON.stringify(Object.assign({ eventName, args }, event));
};

const outgoingResponse = (communicationMeta, entityData = []) => {
	let data = {
		data: entityData, metaData: {
			phoneNumber: communicationMeta.number || communicationMeta.phoneNumber, taskId: communicationMeta.sysid
		}
	}
	let payload = { "type": "OUTGOING_CALL", data };
	let context = { "payload": payload, "method": "openframe_communication" };
	return context;
}

const getEntitySysID = (query) => {
	var myRegexp = /(sys_id|sysId)=([^&\^]+)/;
	var match = myRegexp.exec(query);
	if (!match || match.length != 3)
		return 0;
	return match[2];
}
export { frameResponse, outgoingResponse, getEntitySysID };
