import RowColumn from './RowColumn/';
import Flex from './Flex/';
import Grid from './Grid/';
import RuleBased from './RuleBased/';
import Free from './Free';
import get from 'lodash/get';

const getLayoutRuntime = model => {
	const type = get(model, 'type');

	switch (type) {
		case 'rowColumn':
			return RowColumn;
		case 'flex':
			return Flex;
		case 'grid':
			return Grid;
		case 'ruleBased':
			return RuleBased;
		case 'free':
			return Free;
		default:
			/* eslint-disable-next-line no-console */
			console.warn(`Unknown layout type ${type}. Nothing will be rendered!`);
			return () => null;
	}
};

export default getLayoutRuntime;
