import { actionTypesToProps } from './constants';

const constructMessage = (event, message) => {
	return event === 'POST_TO_PARTNER' ? JSON.stringify(message) : message;
};

const deconstructMessage = (data) => {
	if (typeof data === 'object') {
		return data;
	}

	const parsed = JSON.parse(data);
	const { args: message, eventName: event } = parsed;
	if (event === 'openframe_set_header_title') {
		if (message && message.title) {
			return {
				event: 'openframe_set_title',
				message
			};
		} else if (message && message.subtitle) {
			return {
				event: 'openframe_set_subtitle',
				message
			};
		}
	}
	return {
		event,
		message
	};
};

const transform = (props, event, message) => {
	const prevData = props[actionTypesToProps[event]] || {};
	return Object.assign({}, prevData, message);
};

export { constructMessage, deconstructMessage, transform };
