import {EXPANDED_ITEMS_SET,LOAD_HIERARCHY_DATA,UPDATE_FULL_TREE, UPDATE_PATH, LOAD_HIERARCHY_SUCCESSED, LOAD_CHILDREN_REQUESTED_SUCCESSED, LOADING_REQUESTED, LOAD_CHILDREN_API} from './constants';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import { createUpdateStateEffect } from '@servicenow/ui-effect-update-state';


export const hierarchyQuery = `query ($tableName: String, $childFieldName: String, $parentFieldName: String, $parentProductSysId: String, $maxChildren: Int, $maxDepth: Int, $currentChildrenCount: Int, $query: String, $selectedSoldProductId: String) {
    AppProductModelHierarchy_Query {
      getHierarchy(tableName: $tableName, childFieldName: $childFieldName, parentFieldName: $parentFieldName, parentProductSysId: $parentProductSysId, maxChildren: $maxChildren, currentChildrenCount: $currentChildrenCount, maxDepth: $maxDepth, query: $query, selectedSoldProductId: $selectedSoldProductId) {
        productsDetails
        productModels {
          id
          sysId
          label
          actions{
              label
              iconName
          }
          childrenAvailable
          children {
            id
            sysId
            label
            actions{
                label
                iconName
            }
            childrenAvailable
            
          }
        }
      }
    }
  }
  `;
const loadHierarchyDataEffect = createGraphQLEffect(hierarchyQuery, {
    variableList: ['tableName', 'childFieldName', 'parentFieldName','parentProductSysId', 'maxChildren','currentChildrenCount','maxDepth','query', 'selectedSoldProductId'],
    successActionType: LOAD_HIERARCHY_SUCCESSED
})

const loadChildrensEffect = ({action, dispatch, state}) => {
    dispatch(UPDATE_PATH, { path : action.payload.path })
    var maxDepth = state.maxDepth;
    var query = state.properties.query;
    var currentChildrenCount = 0;
    var maxChildren =state.properties.maxChildren;
    if(action.payload.item.children && action.payload.item.children.length != 0){
        maxDepth = 1;
        maxChildren = action.payload.item.children? parseInt(action.payload.item.children.length) + parseInt(state.properties.maxChildren) : state.properties.maxChildren
        currentChildrenCount = parseInt(action.payload.item.children.length);
    }
    dispatch(LOAD_CHILDREN_API, 
        {
            tableName: state.properties.tableName, 
            childFieldName: state.properties.childFieldName, 
            parentFieldName: state.properties.parentFieldName,
            parentProductSysId: action.payload.item.sysId, 
            maxChildren: maxChildren,
            currentChildrenCount : currentChildrenCount,
            maxDepth: maxDepth, 
            query: query,
            selectedSoldProductId: state.properties.sysId
        });
    
}


const appendChildrens = (fullTree, path, item, index) => {
    for(var i = 0; i < fullTree.length; i++){
        if(fullTree[i].id == path[index] && index < path.length){
            if(index == path.length-1 && !fullTree[i].children)
                fullTree[i].children = item;
            else if(index == path.length-1 && fullTree[i].children)
                fullTree[i].children = fullTree[i].children.concat(item);
            else
                appendChildrens(fullTree[i].children, path, item, index+1)
        }
    }
}

const getChildren = (fullTree, path, index) => {
    for(var i = 0; i < fullTree.length; i++){
        if(fullTree[i].id == path[index] && index < path.length){
            if(index == path.length-1)
                return fullTree[i].children;
            else
            return getChildren(fullTree[i].children, path,  index+1)
        }
    }
}

const updateExpandedTreeEffect = ({action, dispatch, state}) => {
    var item = action.payload.data.AppProductModelHierarchy_Query.getHierarchy[0].productModels;
    var fullTree = JSON.parse(JSON.stringify(state.fullTree));
    appendChildrens(fullTree, state.path, item, 0);
    dispatch(UPDATE_FULL_TREE, {fullTree: fullTree});
}


const loadRequestedChildrensEffect = createGraphQLEffect(hierarchyQuery, {
    variableList: ['tableName', 'childFieldName', 'parentFieldName', 'parentProductSysId', 'maxChildren','currentChildrenCount','maxDepth','query','selectedSoldProductId'],
    successActionType: LOAD_CHILDREN_REQUESTED_SUCCESSED 
})



export const actionHandlers =  {
    [LOAD_HIERARCHY_DATA] : {...loadHierarchyDataEffect},
    
    [LOAD_HIERARCHY_SUCCESSED]: ({action, updateState}) => {
        updateState({
            fullTree : action['payload']['data']['AppProductModelHierarchy_Query']['getHierarchy'][0]['productModels'],
            productsDetails : action['payload']['data']['AppProductModelHierarchy_Query']['getHierarchy'][0]['productsDetails'],
            expandedItems: [[action['payload']['data']['AppProductModelHierarchy_Query']['getHierarchy'][0]['productModels'][0]['id']]]
        })
    }, 

    [LOADING_REQUESTED] : {
        
        effect: loadChildrensEffect
    
    },

    [LOAD_CHILDREN_API] : loadRequestedChildrensEffect,

    [LOAD_CHILDREN_REQUESTED_SUCCESSED] : {
        effect: updateExpandedTreeEffect
    },
    
    [UPDATE_PATH] : ({action, updateState}) => {
        updateState({
            path : action.payload.path
        })
    },
    
    [UPDATE_FULL_TREE] :  ({action, updateState}) => {
        updateState({
            fullTree : action.payload.fullTree
        })
    },

    [EXPANDED_ITEMS_SET] : ({action, updateState}) => {
        updateState({
            expandedItems : action.payload.value
        })
    }
}

