import {LOAD_SELECTED_ITEM_FORM,LOAD_FORM_SUCCEEDED,BUILD_QUERY,UPDATE_FIELDS_LIST} from './constants';

import { createUpdateStateEffect } from '@servicenow/ui-effect-update-state';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';



const formQuery= `query ($queryConditions: String) {
	GlideRecord_Query {
	  sn_install_base_sold_product(queryConditions: $queryConditions) {
		_results {
		name {
			label
			value
			displayValue
		  }
		  {{fields}}
		}
	  }
	}
  }
`;

export const buildQuery=({state,action,dispatch}) =>{
	var fieldsList=state.properties.fields.split(',');
	var queryHolder='';
	for(var i=0;i<fieldsList.length;i++)
		queryHolder = queryHolder + fieldsList[i] + ' { label value displayValue } ';	
	dispatch(LOAD_SELECTED_ITEM_FORM, {
		queryConditions:'sys_id='+state.properties.sysId,
		fields: queryHolder
	});
};



export const loadProductFormEffect = createGraphQLEffect(formQuery, {
	templateVarList: ['fields'],
	variableList: ['queryConditions'],
	successActionType:LOAD_FORM_SUCCEEDED
	});
	

export default {

	[BUILD_QUERY]: buildQuery,

  [LOAD_SELECTED_ITEM_FORM] : loadProductFormEffect,

  [LOAD_FORM_SUCCEEDED]:({action, updateState}) => {
		updateState({
			productDetails: action['payload']['data']['GlideRecord_Query']['sn_install_base_sold_product']['_results'][0]
		})
	}
     
 }
