const renderFree = (children, styles) => {
	return <div style={styles}>{children.map(renderChild)}</div>;
};

const renderChild = child => {
	const { slotName, styles, children = [] } = child;

	return (
		<div style={styles}>
			{slotName ? (
				<div className="slot-wrapper">
					<slot name={slotName} />
				</div>
			) : null}
			{children.map(renderChild)}
		</div>
	);
};

export default ({ model }) => {
	const { children, styles } = model;
	return renderFree(children, styles);
};
