import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { actionTypes } from '@servicenow/ui-core'
import find from 'lodash/find';
import isArray from 'lodash/isArray';
import { t } from 'sn-translate';
import { setItemChildren, togglePath, removePath, getFullUpdatePath } from './utilities';
import {
	LOAD_ACCOUNT,
	LOAD_ACCOUNT_SUCCEEDED,
  LOAD_ACCOUNT_FAILED,
  UPDATE_ACCOUNT,
	UPDATE_ACCOUNT_SUCCEEDED,
  UPDATE_ACCOUNT_FAILED
} from './actions';
import { loadAccountQuery } from './query';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;

// GraphQL Query for initial tree loading
const loadAccountEffect = createGraphQLEffect(loadAccountQuery, {
	successActionType: LOAD_ACCOUNT_SUCCEEDED,
	errorActionType: LOAD_ACCOUNT_FAILED,
	variableList: ['sys_id', 'children', 'update', 'maxChildren']
});

// GraphQL Query for updating the tree
const updateAccountEffect = createGraphQLEffect(loadAccountQuery, {
	successActionType: UPDATE_ACCOUNT_SUCCEEDED,
	errorActionType: UPDATE_ACCOUNT_FAILED,
	variableList: ['sys_id', 'children', 'updatePath', 'update', 'maxChildren']
});

const loadAccountSucceeded = ({state, action, updateState}) => {
  const tags = action['payload']['data']['AppAccountHierarchy_Query']['getHierarchy'];
  
  const rootTags = [tags[0]];
  let itemPathSet = new Set();

  const mapChildren = childId => {
    const tag = find(tags, tag => tag.id === childId) || null;

    if (tag == null) return;
    if (tag.id === state.properties.sysId) {
      itemPathSet.add(tag.parentId);
      itemPathSet.add(state.properties.sysId);
      tag.label = tag.label + ' (' + t('Current Account') + ')';
    }

    if (isArray(tag.children)) {
        if (tag.children.length == 1) {
          itemPathSet.add(tag.id);
        }
        tag.children = tag.children.map(mapChildren).filter(tag => tag !== null).sort((value1, value2) => {
          if (value1.id === state.properties.sysId || itemPathSet.has(value1.id)) {
            itemPathSet.add(tag.id);
            return -1;
          } else if (value2.id === state.properties.sysId || itemPathSet.has(value2.id)) {
            itemPathSet.add(tag.id);
            return 1;
          } else
            return 0;
        });
    } else {
      tag.children = {};
    }

    return tag;
  }

  const tagTree = rootTags.map(tag => {
    tag.children = tag.children.map(mapChildren).filter(tag => tag !== null).sort((value1, value2) => {
      if (value1.id === state.properties.sysId) {
        return -1;
      } else if (value2.id === state.properties.sysId) {
        return 1;
      } else
        return 0;
    });

    return tag;
  });

  let parentItem = tagTree[0];
  let itemArray = Array.from(itemPathSet);
  itemArray.unshift(tagTree[0].id);
  let itemReduce = itemArray.slice(1, itemArray.length - 1);
  itemReduce.forEach(item => {
    for (let i = 0; i < parentItem.children.length; i++) {
      if (parentItem.children[i].id === item) {
        parentItem = parentItem.children[i];
        break;
      }
    }
  });
  let parentTree = [parentItem];

  let itemPathArray = Array.from(itemArray);//.reverse();
  itemPathArray = itemPathArray.slice(0, itemPathArray.length - 1);
  let parentPath = [];
  let baseArray = [];

  itemPathArray.map(item => {
    baseArray.push(item);
    parentPath.push(baseArray.slice());
  });

  updateState({
    ...state,
    fullTree: tagTree,
    itemPath: itemArray,
    parentTree,
    parentExpanded: [[parentTree[0].id]],
    expanded: parentPath
   });
};

const updateAccountSucceeded = ({state, action, updateState}) => {
  const { loadingPath, parentViewOnly, itemPath } = state;
  let { fullTree } = state;
  let updatePath = action.meta.options.variables.updatePath;
  const tags = action['payload']['data']['AppAccountHierarchy_Query']['getHierarchy'];
  const {path, children} = action.payload;
  
  let loading = tags[0];
  let items = [];
  for (let i = 1; i < tags.length; i++) {
    items.push(tags[i]);
  }

  let fullPath;
  if (parentViewOnly) {
    fullPath = getFullUpdatePath(fullTree, updatePath);
    fullTree = setItemChildren(fullTree, fullPath, items);
  } else {
    fullTree = setItemChildren(fullTree, updatePath, items);
  }

  let parentItem = fullTree[0];
  let itemArray = Array.from(itemPath);
  itemArray.unshift(fullTree[0].id);
  let itemReduce = itemArray.slice(1, itemArray.length - 1);
  itemReduce.forEach(item => {
    for (let i = 0; i < parentItem.children.length; i++) {
      if (parentItem.children[i].id === item) {
        parentItem = parentItem.children[i];
        break;
      }
    }
  });
  let parentTree = [parentItem];

  updateState({
    ...state,
    fullTree,
    parentTree,
    loadingPath: togglePath(loadingPath, updatePath)
  });
};

export const actionHandlers = {
  [LOAD_ACCOUNT]: {
    ...loadAccountEffect
  },
  [LOAD_ACCOUNT_SUCCEEDED]: {
    effect: loadAccountSucceeded
  },
  [LOAD_ACCOUNT_FAILED]:  {
    effect: ({ action, updateState }) => {
      console.log('load account failed');
    }
  },
  [UPDATE_ACCOUNT]: {
    ...updateAccountEffect
  },
  [UPDATE_ACCOUNT_SUCCEEDED]: {
    effect: updateAccountSucceeded
  },
  [UPDATE_ACCOUNT_FAILED]:  {
    effect: ({ action, updateState }) => {
      console.log('update account failed');
    }
  },
  [COMPONENT_BOOTSTRAPPED]: {
    effect: ({ state, dispatch }) => {
      const { sysId, maxChildren } = state.properties;
      dispatch(LOAD_ACCOUNT, {
        sys_id: sysId,
        maxChildren: maxChildren
      });
    }
  }
};
