import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	MULTIPLE_USER_RECORDS_FETCHED,
	USER_RECORD_FETCHED,
	USER_RECORD_FETCH_SUCCEEDED,
	USER_RECORD_FETCH_FAILED,
	RECORD_CONFIG_TYPE
} from '../constants';

import {safelyGetValue} from '../utils/context';
import {isUndefined} from '../utils/';

const query = `query ($sys_id: String!) {
	GlideDomain_Query {
	  userInfo(sys_id: $sys_id) {
		avatar
		timeZone
	  }
	}
}`;

const fetchUserInfoEffect = createGraphQLEffect(query, {
	variableList: ['sys_id', 'id'],
	successActionType: USER_RECORD_FETCH_SUCCEEDED,
	errorActionType: USER_RECORD_FETCH_FAILED,
});


export const recordActionHandlers = {
	[MULTIPLE_USER_RECORDS_FETCHED]: ({
		state: {
			layout: { cards_info },
		},
		dispatch,
	}) =>
		cards_info &&
		cards_info.forEach(({ subheader_fields, id, type, isUser }) =>
			type == RECORD_CONFIG_TYPE && isUndefined(isUser)
				? dispatch(USER_RECORD_FETCHED, {
					sys_id: safelyGetValue(['sys_id'], subheader_fields),
					id,
				  })
				: null
		),
	[USER_RECORD_FETCHED]: fetchUserInfoEffect,

	[USER_RECORD_FETCH_SUCCEEDED]: ({
		action,
		state: {
			layout: { cards_info },
		},
		updateState,
	}) => {
		const id = safelyGetValue(['meta', 'options', 'variables', 'id'], action);
		const userInfo = safelyGetValue(
			['payload', 'data', 'GlideDomain_Query', 'userInfo'],
			action
		);
		userInfo
			? updateState({
				path: 'layout.cards_info',
				value: cards_info.map((card) =>
					card.id == id
						? {
							...card,
							isUser: true,
							avatar: userInfo.avatar,
							timeZone: userInfo.timeZone,
							  }
						: card
				),
				operation: 'merge',
			  })
			: updateState({
				path: 'layout.cards_info',
				value: cards_info.map((card) =>
					card.id == id ? { ...card, isUser: false } : card
				),
				operation: 'merge',
			  });
	},
};
