import { createRef } from '@servicenow/ui-renderer-snabbdom';
import { get } from 'lodash';
import { frameResponse } from './openFrameEventsHelper';
import { OPENFRAME_ACTIONS } from "./openframe.effects";
import { t } from 'sn-translate';
import style from './_componentOpenframe.scss';
import "@servicenow/now-legacy-icon";
import '../openframeWrapper/openframeWrapperElement'

function _getCSSClassNameForMainFrame(state) {
	if (!state.properties.isLegacy) {
		if (get(state.properties.configuration, 'showFrame', false)) {
			return state.properties.configuration.position ?
				state.properties.configuration.position :
				'bottom-left';
		}
		return 'hide-frame';
	} else {
		if (get(state, 'local.visible', false) && get(state, 'config.showFrame')) {
			if (state.properties.position) {
				return state.properties.position;
			} else {
				return 'bottom-left';
			}
		}
		return 'hide-frame';
	}
}

export const iconClicked = (pm, icon) => {
	pm.post('openframe_header_icon_clicked', frameResponse('openframe_header_icon_clicked', icon));
	pm.post('openframe_icon_clicked', frameResponse('openframe_icon_clicked', icon));
};
export const headerIconClicked = (pm, titleIcon) => {
	pm.post('openframe_header_icon_clicked', frameResponse('openframe_header_icon_clicked', titleIcon));
	pm.post('openframe_title_icon_clicked', frameResponse('openframe_title_icon_clicked', titleIcon));
};
export const resizeIconClicked = (dispatch, pm, newFrameMode) => {
	dispatch(OPENFRAME_ACTIONS.OPENFRAME_SET_FRAME_MODE, newFrameMode ? { 'message': 'collapse' } : { 'message': 'expand' })
};

export const view = (state, dispatch) => {
	let { config, openframeSetTitle, openframeSetSubtitle, openframeRequest, openframeSetHeaderTitleIcon, openframeSetIcons, openInteraction, openframeSetSize, openframeSetHeight, openframeSetWidth } = state;
	let visible = _getCSSClassNameForMainFrame(state);
	let headerStyle = {
		width: get(openframeSetSize, 'width') ? openframeSetSize.width : '320px',
		height: '100px'
	};
	let finalHeight, finalWidth;
	if (openframeSetSize && openframeSetSize.height) {
		finalHeight = openframeSetSize.height;
	}
	if (openframeSetSize && openframeSetSize.width) {
		finalWidth = openframeSetSize.width;
	}
	let titleicon = {imageURL:''};
	let isLegacy = state.properties.isLegacy;
	let { height: frameHeight, width: frameWidth, title: defaultTitle, subTitle: defaultSubTitle, iframeAllow: iframeAllow, collapsedViewEnabled: collapsedViewEnabled, titleIcon: defaultTitleIcon } = isLegacy ? get(state, 'config.config', '') : get(state, 'properties.configuration.config', '') ;

	let bodyStyle = {
		width: (finalWidth ? finalWidth : frameWidth) + 'px',
	};
	titleicon.imageURL = defaultTitleIcon;
	titleicon = (get(state, 'openframeSetHeaderTitleIcon.imageURL', false)) ? state.openframeSetHeaderTitleIcon : titleicon;

	let frameStyle = {
		width: (finalWidth ? finalWidth : frameWidth) + 'px',
		height: (finalHeight ? finalHeight : frameHeight) + 'px'
	};
	let hostOrigin = state.hostOrigin;
	let iframeAllowAttr = iframeAllow ? iframeAllow : '';
	const iframeRef = createRef();
	let footerStyle = { height: Math.ceil(get(openframeSetIcons, 'message.length', 0) / 5) * 50 + 'px' };
	let showCollapseExpand = collapsedViewEnabled==='true';
	let showOpenframeWrapper = state.initializeWrapper;
	const opened = !isLegacy ? state.properties.opened : true;
	return (
		<section className="sn-component-openframe" hidden={!opened }>
			{showOpenframeWrapper ? (!isLegacy ? <sn-openframe-wrapper user={state.properties.user}></sn-openframe-wrapper> : <sn-openframe-wrapper></sn-openframe-wrapper>) : null}
			<div className={`sn-openframe sn-openframe-mainFrame ${visible}`} style={bodyStyle}>
				<div className="handle sn-openframe-header">
					<div className="sn-openframe-header-content -static">
						<span tabIndex={0} role="button" on-click={() => { headerIconClicked(state.pm, state.openframeSetHeaderTitleIcon); }}
							onkeydown={(event) => { if (event.key == 'Enter') { headerIconClicked(state.pm, state.openframeSetHeaderTitleIcon); } }}>
							{get(titleicon, 'imageURL', false)
								? <img src={titleicon.imageURL} aria-label={titleicon.imageTitle} />
								: null}
						</span>
					</div>
					<div className="sn-openframe-header-content">
						<span>
							<strong>
								{get(openframeSetTitle, 'title', false) || defaultTitle}
							</strong>
						</span>
						<span>
							{get(openframeSetSubtitle, 'subtitle', false) || defaultSubTitle}
						</span>
					</div>
					{showCollapseExpand ? (
						<div className="sn-openframe-header-content">
							{state.showMinimize.value ? (
								<span tabIndex={0} role="button" on-click={() => { resizeIconClicked(dispatch, state.pm, false); }}
									onkeydown={(event) => { if (event.key == 'Enter') { resizeIconClicked(dispatch, state.pm, false); } }}>
									<now-legacy-icon type="plus-square" weight="bold" size="lg" aria-label={t('Expand')}></now-legacy-icon>
								</span>
							) : (
									<span tabIndex={0} role="button" on-click={() => { resizeIconClicked(dispatch, state.pm, true); }}
										onkeydown={(event) => { if (event.key == 'Enter') { resizeIconClicked(dispatch, state.pm, true); } }}>
										<now-legacy-icon type="minus-square" weight="bold" size="lg" aria-label={t('Collapse')}></now-legacy-icon>
									</span>
								)}
						</div>) : (<div className="sn-openframe-header-content" />)
					}
				</div>
				<div className="sn-openframe-frame" style={{ height: ((finalHeight ? finalHeight : frameHeight) - 1) + 'px' }} >
					{hostOrigin ? <iframe src={hostOrigin} id="iframe" className="frame" aria-label={t('Openframe')} ref={iframeRef}
						style={frameStyle}
						allow={iframeAllowAttr} src={hostOrigin}
						on-load={() => { dispatch(OPENFRAME_ACTIONS.OPENFRAME_LOCAL_IFRAME_LOAD, { contentWindow: iframeRef.current.contentWindow }) }}
					/> : null}
				</div>
				{<div className="sn-openframe-footer" style={footerStyle}>
					{get(openframeSetIcons, 'message', []).map((item) => {
						return (
							<button type="button" className="sn-openframe-footer-btn" key={item.imageURL} on-click={() => {
								iconClicked(state.pm, item);
							}}><img src={item.imageURL} aria-label={t(item.imageTitle)} /></button>
						);
					})}
				</div>}
			</div>

		</section>
    );
};


