import { get } from 'lodash';

export const contactContextHelper = (results) => {
	if (!results) {
		return [];
	}
	let entityData = [];

	let contactId = get(results, 'sys_id.value', false);
	if (contactId) {
		entityData.push({
			entity: results.sys_class_name.value,
			query: `sys_id=${results.sys_id.value}`,
			value: results.name.displayValue,
			label: results.sys_class_name.displayValue
		});
	}

	let accountId = get(results, 'account._reference.sys_id.value', false);
	if (accountId) {
		entityData.push({
			entity: results.account._reference.sys_class_name.value,
			query: `sys_id=${accountId}`,
			value: results.account._reference.name.displayValue,
			label: results.account._reference.sys_class_name.displayValue
		});
	}
	return entityData;
};

export const contactInteractionHelper = (results) => {
	if (!results) {
		return {};
	}
	let interaction = {
		interactionSysId: -1,
		direction: 'outbound'
	};

	let contact = get(results, 'sys_id.value', false);
	if (contact) {
		interaction.context_document = contact;
		interaction.context_table = results.sys_class_name.value;
		interaction.contact = contact;
	}

	let account = get(results, 'account._reference.sys_id.value', false);
	if (account) {
		interaction.account = account;
	}
	return interaction;
};