import '@servicenow/now-card';
import '@servicenow/now-heading';
import 'sn-component-visualization';
import '@servicenow/now-button';
import styles from './styles.scss';
import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';

const view = ({
	properties: {
		title,
		mainTitle,
		dataConfigurations,
		styleConfigurations,
		vizKey,
		id,
		slotName,
		variant,
	},
}) => (
	<div
		slot={slotName}
		class={{
			'sn-customer-information-report': true,
			[`sn-customer-information-report-${
				variant && variant.toLowerCase()
			}`]: variant,
		}}
	>
		<div
			class={{
				'sn-customer-information-report-title': true,
				'has-title': !mainTitle,
			}}
		>
			{mainTitle ? (
				<now-heading
					hasNoMargin
					wontWrap
					label={title}
					variant="header-secondary"
					level="2"
				/>
			) : (
				<h3>{title}</h3>
			)}
			<now-button-iconic
				append-to-payload={{ id }}
				size="sm"
				bare={true}
				variant="tertiary"
				icon="sync-outline"
				class={{
					'sn-customer-information-report-refresh': true,
				}}
				config-aria={{
					'aria-label': t('Refresh {0}', title || t('content')),
				}}
			/>
		</div>
		<div className="sn-customer-information-report-content">
			<sn-component-visualization
				key={vizKey}
				variant={variant}
				border={true}
				label={title}
				chartType={dataConfigurations['type']}
				dataConfigurations={[dataConfigurations]}
				styleConfigurations={styleConfigurations}
				auto-click-behaviour
			/>
		</div>
	</div>
);
/**
 * A wrapper around visualization component
 * 
 * ```
 * <sn-customer-information-visual />
 * ``` 
 * @seismicElement sn-customer-information-visual
 * @summary A wrapper around visualization component
 * 
 */
createCustomElement('sn-customer-information-visual', {
	renderer: { type: snabbdom },
	view,
	properties: {
		slotName: {
			default: '',
			schema: { type: 'string' },
		},
		variant: {
			default: 'SMALL',
			schema: { type: 'string' },
		},
		mainTitle: {
			default: true,
			schema: { type: 'boolean' },
		},
		title: {
			default: '',
			schema: { type: 'string' },
		},
		dataConfigurations: {
			default: {},
			schema: { type: 'object' },
		},
		styleConfigurations: {
			default: {},
			schema: { type: 'object' },
		},
		vizKey: {
			default: undefined,
			schema: { type: 'string' },
		},
		id: {
			default: undefined,
			schema: { type: 'string' },
		},
	},
	styles,
});

const renderVisual = ({
	slotName,
	visualJSON: {
		title,
		data_configurations: dataConfigurations,
		style_configurations: styleConfigurations,
		vizKey,
		id,
	},
	variant = 'MEDIUM',
	mainTitle = true,
}) => (
	<sn-customer-information-visual
		key={id}
		mainTitle={mainTitle}
		title={title}
		dataConfigurations={dataConfigurations}
		styleConfigurations={styleConfigurations}
		vizKey={vizKey}
		id={id}
		slotName={slotName}
		variant={variant}
		slot={slotName}
	/>
);

export { renderVisual };
