import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import mediaQueryBehavior from '@servicenow/behavior-media-query';
import slottedPolyfillBehavior from './slotted-polyfill-behavior';
import childrenWithSlotBehavior from './children-with-slot-behavior';

import get from 'lodash/get';
import isString from 'lodash/isString';

import getLayoutRuntime from './runtimes';
import styles from './styles.scss';
import { getModelByDevice } from './runtimes/getModelByDevice';

const parseModel = (rawModel = null) => {
	if (isString(rawModel)) {
		let model = null;

		try {
			model = JSON.parse(rawModel);
		} catch (e) {
			/* eslint-disable-next-line no-console */
			console.warn('Unable to parse model in sn-layout');
		}

		return model;
	} else {
		return rawModel;
	}
};

const view = (state, dispatch) => {
	const model = parseModel(get(state, 'properties.model'));

	if (!model) return null;

	const { modelByDevice, isDesktopModel } = getModelByDevice(
		model,
		state.behaviors.mediaQuery
	);

	const runtimeFn = getLayoutRuntime(modelByDevice);
	return runtimeFn({
		...state.properties,
		model: modelByDevice,
		isMobile: !isDesktopModel,
		dispatch,
		ruleBasedRows: state.ruleBasedRows,
		currentSlots: state.currentSlots,
		childrenWithSlot: state.behaviors.childrenWithSlot.slots
	});
};

const TAG_NAME = 'sn-layout';
createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		model: {},
		parentTag: {
			reflect: true
		},
		renderers: {
			default: {
				column: null,
				row: null
			}
		},
		collapsibleColumns: []
	},
	initialState: {
		ruleBasedRows: [],
		currentSlots: undefined, // slots the DOM has rendered for ruleBased layouts
		resizeFn: null,
		mutationObserver: null
	},
	styles,
	behaviors: [
		{
			behavior: slottedPolyfillBehavior,
			options: {
				selector: '[slot]'
			}
		},
		{
			behavior: mediaQueryBehavior,
			options: {
				isMobile: '(max-width: 480px)',
				isTablet: '(max-width: 1024px)',
				isDesktop: '(min-width: 1024px)'
			}
		},
		{
			behavior: childrenWithSlotBehavior
		}
	],
	actionHandlers: {
		CALCULATE_RULE_BASED_ROW({ action, updateState }) {
			updateState({
				ruleBasedRows: action.payload.rows,
				currentSlots: action.payload.slots
			});
		},
		SET_MUTATION_OBSERVER_AND_RESIZE_FN({ action, updateState }) {
			const resize = action.payload.resizeFn();
			window.addEventListener('resize', resize);

			const observer = new MutationObserver(() => {
				action.payload.callback();
			});

			observer.observe(action.payload.host, {
				childList: true,
				attributes: true,
				subtree: true
			});
			updateState({
				mutationObserver: observer,
				resizeFn: resize
			});
		},
		UNSET_MUTATION_OBSERVER_AND_RESIZE_FN({ state, updateState }) {
			window.removeEventListener('resize', state.resizeFn);
			state.mutationObserver.disconnect();
			updateState({
				mutationObserver: null,
				resizeFn: null
			});
		}
	}
});

export default TAG_NAME;
