import { t } from 'sn-translate';
import { PREVIEW_RECORD } from './actions';

export const view = (state, dispatch) => {
	const { fullTree, clickedId, itemPath, parentViewOnly,
		parentTree, loadingPath, expanded, parentExpanded
	} = state;

	if (fullTree == null || fullTree.length == 0) return <div><now-loader label={t('Loading')}></now-loader></div>;

	if (clickedId) {
		dispatch(PREVIEW_RECORD, {table: 'customer_account', sys_id: clickedId});
	}

	let accountInfo = '';
	let itemArray = Array.from(itemPath);//.reverse();
	let parentPath = [];
	let baseArray = [];
	itemArray.map(item => {
		baseArray.push(item);
		parentPath.push(baseArray.slice());
	});

	let treeView = parentViewOnly ? parentTree : fullTree;

	return (
        <div className="sn-account-hierarchy">
			<sn-component-hierarchy-display items={fullTree} parent-Tree={parentTree} size="lg" expanded={expanded} parent-expanded={parentExpanded} loading-items={[{path: loadingPath}]}></sn-component-hierarchy-display>
		</div>
    );
};
