import { createCustomElement } from '@seismic/core';
import view from './view';
import actionHandlers from './actions';
import styles from './casetypes-selector.scss';
import { t } from 'sn-translate';


createCustomElement('sn-casetypes-selector', {
    view,
    styles,    
    properties: {
        heading: {
            default: t('Select a Case Type. To narrow the available choices in the Case Type list, you can optionally select a Category.')
        },
        selectedEntityDisplayVal: {
            reflect: true,
            default: ''
        },
        category: {
            default: {
                label: t('Category'),
                required: false                
            }
        },
        caseTypes: {
            default: {
                label: t('Case Type'),
                required: true
            }
        },
        subtypes: {
            default: {
                label: '',
                required: false
            }
        },
        selectedCategory: {
            default: { value: '', displayValue: '' }
        },
        selectedCaseTypes: {
            default: { value: '', displayValue: '' }
        },
        selectedSubtype: {
            default: { value: '', displayValue: '' }
        },
        sourceTable:{
            default: ''
        },
        sourceId:{
            default: ''
        }
    },
    initialState: {
        parentResults: [],
        childResults: [],
        subtypeResults: [],
        searchTerm: '',
        subtypeSearchTerm: '',
        selectedEntityDisplayVal: '',
        selectedSubtypeEntityDisplayValue: '',
        subtypeToggled: false,
        selectedSubtypeField: ''
    },
    actionHandlers
});
