import {LOAD_HIERARCHY_DATA,ACTION_CLICKED, PREVIEW_RECORD,TREE_ITEM_SELECTED, UPDATE_FULL_TREE, TREE_ITEM_CLICKED, UPDATE_PATH, SEARCH_STRING_CLEARED, LOAD_HIERARCHY_SUCCESSED, ITEM_SELECTED, LOAD_SEARCHED_TREE, LOAD_CHILDREN_REQUESTED_SUCCESSED,LOAD_SEARCHED_TREE_SUCCESSED, SEARCH_STRING_CHANGED, LOADING_REQUESTED, LOAD_CHILDREN_API} from './constants';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import { createUpdateStateEffect } from '@servicenow/ui-effect-update-state';
import { actionTypes } from '@servicenow/ui-core';

const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED } = actionTypes;
export const hierarchyQuery =`query($tableName: String, $displayFieldName: String, $query: String, $parentFieldName: String,  $maxChildren: Int,$maxDepth: Int, $parentSysId: String) {
    AppSoldProductHierarchy_Query {
        getHierarchy(tableName: $tableName, displayFieldName: $displayFieldName, query: $query, parentFieldName: $parentFieldName, maxChildren: $maxChildren,maxDepth:$maxDepth, parentSysId: $parentSysId) {
            id
            label
            childrenAvailable
            actions {
                label
                iconName
            }
            children {
                id
                label
                childrenAvailable
                actions {
                    label
                    iconName
                }
            }
        }
    }
}
`
const loadHierarchyDataEffect = createGraphQLEffect(hierarchyQuery, {
    variableList: ['tableName', 'displayFieldName', 'query', 'parentFieldName', 'maxChildren', 'parentSysId'],
    successActionType: LOAD_HIERARCHY_SUCCESSED
})

const loadChildrensEffect = ({action, dispatch, state, updateProperties, properties}) => {
    //updateState({path: action.payload.path})
    updateProperties({path: action.payload.path})
    var maxDepth = state.maxDepth;
    var query = properties.query;
    var currentChildrenIds = [];

    if(action.payload.item.children && action.payload.item.children.length!=0){
        maxDepth=1;
        var currentChildren = getChildren(JSON.parse(JSON.stringify(state.fullTree)), action.payload.path, 0);
        for(var i =0;i<currentChildren.length;i++)
            currentChildrenIds.push(currentChildren[i].id);
        query = query + '^sys_idNOT IN'+currentChildrenIds.join();
    }
    dispatch(LOAD_CHILDREN_API, 
        {
            parentSysId: action.payload.item.id, 
            maxChildren: properties.maxChildren,
            maxDepth:maxDepth, 
            tableName: state.properties.tableName, 
            parentFieldName: properties.parentFieldName,
            displayFieldName: properties.displayFieldName, 
            query: query
        });
    
}


const appendChildrens = (fullTree, path, item, index) => {
    for(var i = 0; i < fullTree.length; i++){
        if(fullTree[i].id == path[index] && index < path.length){
            if(index == path.length-1 && !fullTree[i].children)
                fullTree[i].children = item;
            else if(index == path.length-1 && fullTree[i].children)
                fullTree[i].children = fullTree[i].children.concat(item);
            else
                appendChildrens(fullTree[i].children, path, item, index+1)
        }
    }
}

const getChildren = (fullTree, path, index) => {
    for(var i = 0; i < fullTree.length; i++){
        if(fullTree[i].id == path[index] && index < path.length){
            if(index == path.length-1)
                return fullTree[i].children;
            else
            return getChildren(fullTree[i].children, path,  index+1)
        }
    }
}


const updateExpandedTreeEffect = ({action, dispatch, state, properties}) => {
    var item = action.payload.data.AppSoldProductHierarchy_Query.getHierarchy;
    var fullTree = JSON.parse(JSON.stringify(state.fullTree));
    appendChildrens(fullTree, properties.path, item, 0);
    dispatch(UPDATE_FULL_TREE, {fullTree: fullTree});
    
}



const loadRequestedChildrensEffect = createGraphQLEffect(hierarchyQuery, {
    variableList: ['tableName', 'displayFieldName', 'query', 'parentFieldName', 'maxChildren','maxDepth', 'parentSysId'],
    successActionType: LOAD_CHILDREN_REQUESTED_SUCCESSED 
})


const loadSearchedTreeEffect = createHttpEffect('api/sn_pro_cs_ops/hierarchy_data_search/searchItem', {
    method : 'GET',
    queryParams: ['tableName', 'displayFieldName', 'query', 'parentFieldName', 'maxChildren', 'searchedSysId'],
    successActionType: LOAD_SEARCHED_TREE_SUCCESSED 
})

const itemClickedEffect = ({action, dispatch, state}) => {
    dispatch(TREE_ITEM_SELECTED, {treeItem : action.payload.item})
}

const openRecordEffect=({action,dispatch,state}) => {
    dispatch(PREVIEW_RECORD, {table: 'sn_install_base_sold_product', sys_id: action['payload']['item']['id']});
}

export const actionHandlers =  {
    [COMPONENT_BOOTSTRAPPED]: ({dispatch, properties}) => {
            if(properties.searchedSysId){
                dispatch(LOAD_SEARCHED_TREE, { 
                    searchedSysId: properties.searchedSysId, 
                    tableName: properties.tableName, 
                    parentFieldName: properties.parentFieldName
                })
            }
            else {
                dispatch(LOAD_HIERARCHY_DATA, {
                    tableName: properties.tableName,
                    displayFieldName: properties.displayFieldName,
                    query: properties.query,
                    parentSysId: "",
                    parentFieldName: properties.parentFieldName,
                    maxChildren: properties.maxChildren,
                    maxDepth:2
                });
            }
    },

    [COMPONENT_PROPERTY_CHANGED]: ({ action, dispatch }) => {
        const { payload: {name, value}} = action;
        if (name == 'sysId'){
            dispatch(LOAD_SEARCHED_TREE, { 
				searchedSysId: value, 
				tableName: 'sn_install_base_sold_product', 
				parentFieldName: 'parent_sold_product',
				displayFieldName: "name",
				query: "",
				maxChildren: 5,
				label: "Sold Product"
			})
        }
           
    },

    [LOAD_HIERARCHY_DATA] : {...loadHierarchyDataEffect},

    [LOAD_HIERARCHY_SUCCESSED]: ({action, updateState}) => {
        updateState({
            fullTree : action.payload.data.AppSoldProductHierarchy_Query.getHierarchy
        })
    }, 

    
    [LOAD_SEARCHED_TREE] : loadSearchedTreeEffect,

    [LOAD_SEARCHED_TREE_SUCCESSED] : ({action, updateState}) => {
        updateState({
            searchedTree : action.payload.result.searchedTree,
            expandedPath : action.payload.result.expandedPath,
            selectedPath: action.payload.result.selectedPath})
    },

    [LOADING_REQUESTED] : {
        effect: loadChildrensEffect
    },

    [LOAD_CHILDREN_API] : loadRequestedChildrensEffect,

    [LOAD_CHILDREN_REQUESTED_SUCCESSED] : {
        effect: updateExpandedTreeEffect
    },
     
    [SEARCH_STRING_CLEARED] : ({updateState}) => {
        updateState({
            searchedTree: []})
    },

    [TREE_ITEM_CLICKED] : {
        effect : itemClickedEffect
    },

    
    [UPDATE_FULL_TREE] : ({action, updateState}) => {
        updateState({
            fullTree : action.payload.fullTree})
    },
    
    [ACTION_CLICKED] : openRecordEffect
}