import get from 'lodash/get';

export const getModelByDevice = (model, queryVars) => {
	const { isMobile, isTablet } = queryVars;
	const mobileModel = get(model, 'mobile');
	const tabletModel = get(model, 'tablet');
	const defaultModel = get(model, 'default');
	let modelByDevice;

	if (isMobile) {
		modelByDevice = mobileModel || tabletModel || defaultModel;
	} else if (isTablet) {
		modelByDevice = tabletModel || defaultModel || mobileModel;
	} else {
		modelByDevice = defaultModel || tabletModel || mobileModel;
	}

	return {
		modelByDevice,
		isDesktopModel: modelByDevice === defaultModel
	};
};
