import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';
import properties from './properties';
import actions from './actions';
import actionHandlers from './actionHandlers';
import {FACET_CONSTANTS, FEED_CONSTANTS} from './constants';
import behaviors from './behaviors';

createCustomElement('sn-customer-feed', {
	renderer: {type: snabbdom},
	view,
	styles,
	initialState: {
		filters: {
			facet: null, // Selected Facet, represents a subscribable object.
			facetId: null // Instance of Facet, sys_id of the record. Ex: case sys_id.
		},
		data: {
			facets: [],
			feedInfo: {
				[FACET_CONSTANTS.ALL_ACTIVITY_HEAD]: {
					[FEED_CONSTANTS.FEED_HEAD]: {
						activitiesCount: 0
					}
				}
			},
			feed: {
				activities: [],
				hasMoreRecords: false
			},
			selectedFacet: 0, /* FACET_CONSTANTS.ALL_ACTIVITY_HEAD*/
			expandedFacet: null,
			loadingFacets: true,
			loadingChildFacets: {}, //Using object to avoid collision with multiple selections.
			isFeedLoading: true,
			selectedFacets: '',
			nextFocusableKey: '',
			selectedRecordId: '',
			showAllChildrenFor: [],
			random: null
		},
		startDate: null,
		endDate: null,
		kickStartElement: []
	},
	behaviors,
	properties,
	actions,
	actionHandlers
});
