import {nowContentTree} from "@servicenow/now-content-tree";
import {nowHeading} from "@servicenow/now-heading";
import '@servicenow/now-tooltip';
import {EXPANDED_ITEMS_SET} from "./constants";
function getSelectedPath(state){
    if(state.productsDetails.length==0)
        return;
    var mid = (state.productsDetails.length)/2;
    var selectedPath = "" + state.productsDetails[mid];
    for(var i = mid + 1; i < state.productsDetails.length; i++)
        selectedPath = selectedPath + " > " + state.productsDetails[i];
    return selectedPath;
}

function getSelectedItems(state){
    return state.productsDetails.slice(0,(state.productsDetails.length)/2);
}

function showTooltip(dispatch,element,label){
	if (element.scrollWidth > element.clientWidth) {
		dispatch.updateState({
			path: 'currentTooltip',
			operation: 'set',
			value: {ref: element, content: label, opened: true}
		});
	}
}

const hideTooltip = (dispatch) => {
	dispatch.updateState({
		path: 'currentTooltip',
		operation: 'set',
		value: {ref: null, content: null, opened: false}
	});
};

const view = (state, dispatch) => {
    var selectedPath = getSelectedPath(state);
    var selectedItems = getSelectedItems(state);
    
    return (
        
        <div className="sn-hierarchy">

        {state.fullTree?<p className="ellipsis text--quaternary"
        on-mouseenter={(e) =>
            showTooltip(dispatch, e.currentTarget, selectedPath)
        }
        on-mouseleave={() => hideTooltip(dispatch)}
        >{selectedPath}</p>: null}

        <now-tooltip
				id="tooltip"
				opened={state.currentTooltip.opened}
				target-ref={state.currentTooltip.ref}
                content={state.currentTooltip.content}
		/>
        <div className="tree">
        {state.fullTree?<now-content-tree showDividers={true} items={state.fullTree} select="multi" selected-items={[selectedItems]} expanded-items={state.expandedItems} size='md'> </now-content-tree> : null}
        </div>
        </div>
    );
}

export default view;