import { t } from 'sn-translate';
import "@servicenow/now-modal";

export const view = (state, dispatch) => {
  const {close} = state;
  let open = true;
  if (close) return (<div></div>);//open = false;
  const ahConf = {
    modalId: 'sn-modal-embedded-ah',
    modalTitle: 'Embedded Modal',
    buttons: []
  };
  const modalConfiguration = {...ahConf, actionMetaData:{showPropKey: 'showEmbededModal'}};
  const hierarchyComponent = <sn-component-account-hierarchy sys-id={state.properties.recordSysId}></sn-component-account-hierarchy>;
  return (
    <now-modal size="md" header-label={t('Account Hierarchy')}
        shouldFocusOnOpen={true} modalConfiguration={modalConfiguration}
        context='form'
        opened={open}>{hierarchyComponent}
    </now-modal>
  );
}