import { transformColumns } from './transformColumns';
import { adjustColumns } from './adjustColumns';
import { map } from 'lodash';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

function defaultRenderer(props) {
	return <div className={props.className}>{props.children}</div>;
}

export const getAdjustedColumns = (
	columns,
	fixedColumns = false,
	childrenWithSlot,
	dynamic
) => {
	const columnsToUse = dynamic
		? columns.filter(c => childrenWithSlot.has(c.slotName))
		: columns;
	const transformed = transformColumns(columnsToUse, dynamic, childrenWithSlot);
	const adjusted = adjustColumns(transformed);

	return map(adjusted, (size, i) => {
		const sizeClassNames = fixedColumns
			? `col-xs-${size}`
			: `col-md-${size} col-xs-12`;

		return {
			...columnsToUse[i],
			sizeClassNames
		};
	});
};

export const renderRow = (row, renderers, childrenWithSlot, dynamic) => {
	const { type, slotName, isMobile, divider, columns, id } = row;

	if (type !== 'row') return null;

	const adjustedColumns = getAdjustedColumns(
		columns,
		isMobile,
		childrenWithSlot,
		dynamic
	);

	const className = `row ${divider ? 'divider-row' : ''}`.trim();

	return (renderers.row || defaultRenderer)({
		className,
		id,
		children: (
			<Fragment>
				{map(adjustedColumns, column =>
					renderColumn(column, renderers, childrenWithSlot, dynamic)
				)}
				{slotName &&
					renderColumn(
						{
							type: 'column',
							slotName,
							id
						},
						renderers
					)}
			</Fragment>
		)
	});
};

export const renderColumn = (column, renderers, childrenWithSlot, dynamic) => {
	const {
		type,
		slotName,
		rows,
		divider,
		sizeClassNames = 'col-xs-12',
		styles,
		id
	} = column;

	if (type !== 'column') return null;

	const className = `column ${sizeClassNames} ${
		divider ? 'divider-column' : ''
	}`.trim();

	return (renderers.column || defaultRenderer)({
		className,
		id,
		children: (
			<Fragment>
				{map(rows, row => renderRow(row, renderers, childrenWithSlot, dynamic))}
				{slotName ? (
					<div style={styles}>
						<div className="slot-wrapper">
							<slot name={slotName} />
						</div>
					</div>
				) : null}
			</Fragment>
		)
	});
};

export default ({ model, renderers, childrenWithSlot }) => {
	let { root, dynamic, styles } = model;
	if (root.type !== 'row') {
		root = {
			type: 'row',
			columns: [{ ...root }]
		};
	}
	return (
		<div style={styles}>
			{renderRow(root, renderers, childrenWithSlot, dynamic)}
		</div>
	);
};
