import { calculateSizesAndGroup, getGutterSpace } from './utils';
import { isEqual, debounce, sumBy } from 'lodash';

export default ({ model, dispatch, ruleBasedRows, currentSlots }) => {
	const { rules, items } = model;

	let renderFn = rules.algorithm === 'fillGaps' ? fillGaps : defaultRules;

	return renderFn(items, dispatch, ruleBasedRows, currentSlots);
};

const defaultRules = slots => {
	return <div className="rule-based">{slots.map(renderSlot)}</div>;
};

const recalculateOnResize = (slots, dispatch, elm) =>
	debounce(() => calculateSizesAndGroup(slots, dispatch, elm), 200);

const fillGaps = (slots, dispatch, ruleBasedRows, currentSlots) => {
	return (
		<div
			className="rule-based"
			hook-insert={({ elm }) => {
				calculateSizesAndGroup(slots, dispatch, elm);
				dispatch('SET_MUTATION_OBSERVER_AND_RESIZE_FN', {
					callback: () => calculateSizesAndGroup(slots, dispatch, elm),
					resizeFn: () => recalculateOnResize(slots, dispatch, elm),
					host: elm.parentNode.host
				});
			}}
			hook-update={(o, n) => {
				if (!isEqual(currentSlots, slots)) {
					calculateSizesAndGroup(slots, dispatch, n.elm);
				}
			}}
			hook-remove={(v, removeCallback) => {
				dispatch('UNSET_MUTATION_OBSERVER_AND_RESIZE_FN');
				if (removeCallback) removeCallback();
			}}
			key="rule-based"
		>
			<div
				className="absolute-wrapper"
				hook-update={(o, n) => {
					if (n && isEqual(currentSlots, slots)) {
						n.elm.parentElement.style.height =
							sumBy(ruleBasedRows, 'rowHeight') +
							getGutterSpace() * (ruleBasedRows.length - 1) +
							'px';
					}
				}}
			>
				{ruleBasedRows.length > 0 && isEqual(currentSlots, slots) ? (
					ruleBasedRows.map(renderRow)
				) : (
					<div className="rule-based-first-render">{slots.map(renderSlot)}</div>
				)}
			</div>
		</div>
	);
};

const renderRow = row => {
	return <span className="rule-based-row">{row.groups.map(renderGroup)}</span>;
};

const renderGroup = group => {
	return (
		<span
			className="rule-based-group"
			style={{
				maxWidth: `${group.groupWidth}px`,
				maxHeight: `${group.groupHeight + group.additionalHeight}px`
			}}
		>
			{group.slots.map(s => (s.slots ? renderGroup(s) : renderSlot(s)))}
		</span>
	);
};

const renderSlot = s => {
	const sizing = { height: s.height, width: s.width };
	return (
		<span className="rule-based-slot" style={sizing}>
			<slot name={s.name} />
		</span>
	);
};
