import {
	find,
	minBy,
	isUndefined,
	isNil,
	remove,
	sumBy,
	findIndex,
	reduce,
	partition
} from 'lodash';

let gutterSpace = 0; // gets set based on first render

export const getGutterSpace = () => {
	return gutterSpace;
};

/**
 * 1. Sets the gutterspace after first render
 * 2. Gets the sizes of the contents of each slot and creates a new list
 *    based on the slots list from the json model
 *
 * @param {list} slots list from json model
 * @param {function} dispatch
 * @param {object} elm "rule-based" div
 */
export const calculateSizesAndGroup = (slots, dispatch, elm) => {
	if (gutterSpace === 0) {
		gutterSpace = Math.abs(
			parseInt(
				window.getComputedStyle(elm, null).getPropertyValue('margin-left'),
				10
			) * 2
		);
	}

	const slotList = elm.getElementsByTagName('slot');

	// get the dimensions that we will use to determine groups
	const domSlots = reduce(
		slots,
		(acc, s) => {
			const current = slotList[s.name];
			const assignedNode =
				current && current.assignedNodes
					? current.assignedNodes()[0]
					: undefined;
			if (assignedNode) {
				const rect = assignedNode.getBoundingClientRect();
				return [
					...acc,
					{
						name: s.name,
						width: rect.width,
						height: rect.height
					}
				];
			} else {
				// if slot in the json doesn't exist in dom, we find the dom element this way
				const newSlot = elm.parentNode.host.querySelectorAll(
					`[slot="${s.name}"]`
				)[0];
				if (!isNil(newSlot)) {
					const rect = newSlot.getBoundingClientRect();
					return [
						...acc,
						{
							name: s.name,
							width: rect.width,
							height: rect.height
						}
					];
				}
			}
			return acc;
		},
		[]
	);

	dispatch('CALCULATE_RULE_BASED_ROW', {
		rows: groupSlots(domSlots, elm.getBoundingClientRect().width, gutterSpace),
		slots
	});
};

/**
 * Groups slots and then puts those groups into different rows
 *
 * @param {List} slots the list of slots
 * @param {number} maxWidth The width sn-layout can take up
 * @param {number} gutterSpace The padding between slots
 */
export const groupSlots = (slots, maxWidth, gutterSpace) => {
	const rows = [];
	// things that are too large shouldn't go through buildGroups()
	let [remainders, tooLarge] = partition(
		slots,
		s => s.width + gutterSpace <= maxWidth
	);

	// create new rows as long as there are remainders
	while (remainders.length > 0) {
		const { groups, invalids, rowWidth, rowHeight } = buildGroups(
			remainders,
			maxWidth,
			gutterSpace
		);
		rows.push({ groups, rowWidth, rowHeight });
		remainders = invalids;
	}

	// render things that are too large into their own group at the end
	if (tooLarge.length) {
		const width = sumBy(tooLarge, 'width');
		const height = sumBy(tooLarge, 'height');
		rows.push({
			groups: [
				{
					slots: tooLarge,
					groupHeight: height,
					groupWidth: width
				}
			],
			rowHeight: height,
			rowWidth: width
		});
	}

	return rows;
};

/**
 * Fills a row with groups that can layout in order
 * 1. Renders a row of slots
 * 2. Fills in the gaps left by the slots rendered in the first row
 * 3. Group up slots (and group up groups if necessary) in order to determine
 *    how large any remaining gaps are
 *
 * @param {list} slots list of slots with their heights and widths
 * @param {number} maxWidth
 * @param {number} gutterSpace
 */
const buildGroups = (slots, maxWidth, gutterSpace) => {
	const invalids = [];
	const groups = [];
	let rowHeight = 0;

	// Initial groups, we render one row first as the initial group
	const rowWidth = slots.reduce((acc, s, i) => {
		const slotWidth = parseInt(s.width, 10);
		const slotHeight = parseInt(s.height, 10);
		if (slotHeight > rowHeight) {
			rowHeight = slotHeight;
		}
		if (acc + slotWidth + (i === 1 ? 0 : gutterSpace) <= maxWidth) {
			// group consecutive same-height slots
			const lastGroup = groups[groups.length - 1];
			if (lastGroup && slotHeight === lastGroup.groupHeight) {
				lastGroup.slots.push(s);
				lastGroup.groupWidth += slotWidth + gutterSpace;
				lastGroup.availableWidth += slotWidth + gutterSpace;
			} else {
				groups.push({
					slots: [s],
					groupHeight: slotHeight,
					additionalHeight: 0,
					groupWidth: slotWidth + gutterSpace,
					availableWidth: slotWidth,
					filled: false
				});
			}
			return acc + slotWidth + gutterSpace;
		}
		invalids.push(s);
		return acc;
	}, 0);

	// last group in row should take up the rest of the width available
	const remainingWidth = maxWidth - sumBy(groups, 'groupWidth');
	const lastGroup = groups[groups.length - 1];
	lastGroup.groupWidth += remainingWidth;
	lastGroup.availableWidth += remainingWidth;

	// Fill in the groups until they are filled
	// We do this in order of smallest to largest height for a
	// logical order
	while (find(groups, { filled: false }) && invalids.length > 0) {
		const smallest = minBy(groups.filter(i => !i.filled), 'groupHeight');

		// calculate space left
		const h = rowHeight - smallest.groupHeight;
		const w = smallest.availableWidth;

		// see if anything fits in the space remaining
		const candidate = find(invalids, slot => {
			const slotWidth = parseInt(slot.width, 10);
			const slotHeight = parseInt(slot.height, 10);
			if (slotWidth <= w && slotHeight <= h) {
				return true;
			}
		});

		const currentIndex = findIndex(groups, smallest);

		// If nothing fits in the space remaining
		if (isUndefined(candidate)) {
			// make sure the group doesn't actually have more space
			smallest.groupHeight += smallest.additionalHeight;
			smallest.additionalHeight = 0;
			if (
				smallest.groupHeight !== rowHeight &&
				smallest.availableWidth + gutterSpace !== smallest.groupWidth
			) {
				smallest.availableWidth = smallest.groupWidth - gutterSpace;
			} else {
				// otherwise try to make a supergroup, if unsuccessful, then the group is filled
				smallest.filled = !tryMakeSuperGroup(currentIndex, rowHeight, groups);
			}
		} else {
			// if there is something that fits in the space remaining
			remove(invalids, i => i === candidate);
			const slotWidth = parseInt(candidate.width, 10);
			const slotHeight = parseInt(candidate.height, 10);
			smallest.slots.push(candidate);

			if (slotWidth === smallest.availableWidth) {
				smallest.groupHeight += slotHeight + gutterSpace;
				smallest.availableWidth = smallest.groupWidth;
			} else {
				// if the candidate fills up the available width, we can
				// increase the groupHeight
				if (smallest.availableWidth === smallest.groupWidth) {
					smallest.groupHeight += slotHeight + gutterSpace;
				} else if (smallest.additionalHeight < slotHeight) {
					// if the candidate didn't fill up the available width,
					// we have to try to fill in the available with while
					// making sure we keep track of the height we added
					smallest.additionalHeight = slotHeight + gutterSpace;
				}
				smallest.availableWidth -= slotWidth + gutterSpace;
			}

			// another possible place where we can make a super-group
			tryMakeSuperGroup(currentIndex, rowHeight, groups);
		}
	}

	return {
		groups,
		invalids,
		rowHeight,
		rowWidth
	};
};

/**
 * Makes a super group if possible
 * @param {object} prev the object before smallest in groups
 * @param {object} next the object after smallest in groups
 * @param {object} smallest the smallest height slot/group in groups
 * @param {number} currentIndex index of smallest in groups
 */
const tryMakeSuperGroup = (currentIndex, rowHeight, groups) => {
	let result = false;
	const prev = groups[currentIndex - 1];
	const next = groups[currentIndex + 1];
	let smallest = groups[currentIndex];
	if (
		prev &&
		prev.groupHeight + prev.additionalHeight >=
			smallest.groupHeight + smallest.additionalHeight &&
		prev.groupHeight + prev.additionalHeight !== rowHeight
	) {
		makeSuperGroup(prev, smallest, currentIndex, groups);
		while (
			groups[currentIndex] &&
			groups[currentIndex].groupHeight === prev.groupHeight
		) {
			makeSuperGroup(prev, groups[currentIndex], currentIndex, groups);
		}
		result = true;
	}
	if (
		next &&
		next.groupHeight + next.additionalHeight >=
			smallest.groupHeight + smallest.additionalHeight &&
		next.groupHeight + next.additionalHeight !== rowHeight
	) {
		smallest.slots = [{ ...smallest }];
		while (
			currentIndex + 1 < groups.length &&
			groups[currentIndex + 1] &&
			groups[currentIndex + 1].groupHeight +
				groups[currentIndex + 1].additionalHeight >=
				smallest.groupHeight + smallest.additionalHeight
		) {
			makeSuperGroup(
				smallest,
				groups[currentIndex + 1],
				currentIndex + 1,
				groups
			);
		}
		result = true;
	}
	return result;
};

/**
 * Combines slots and groups into a single group
 * @param {object} parent The base group
 * @param {object} child The slot or group to be added to parent
 * @param {number} childIndex
 */
const makeSuperGroup = (parent, child, childIndex, groups) => {
	parent.slots.push({ ...child });
	parent.availableWidth += child.groupWidth;
	parent.groupWidth += child.groupWidth;
	groups.splice(childIndex, 1);
};
