export const loadAccountQuery = `query($sys_id :ID!, $children :[String], $update :Boolean, $maxChildren : Int){
	AppAccountHierarchy_Query{
		getHierarchy(parentSysId: $sys_id, update: $update, maxChildren: $maxChildren, currentChildren: $children) {
      label
			id
			parentId
      children
      childrenAvailable
			actionable
			noSelect
      actions {
        label
        iconName
        clickActionType
        hoverActionType
      }
    }
	}
}
`;