import { UPDATE_MODULE_STATUS } from './constants';

export function updatePhoneOnToolbar(dispatch, visible, icon_name, isLegacy) {
	const action = {
		id: 'cti',
		show: visible,
		icon: icon_name
	};
	if (isLegacy) {
		dispatch(UPDATE_MODULE_STATUS, action);
	} else {
		dispatch('PRIME_NAV_SOURCE_CHANGED', action);
	}
}

export function updateOpenframeAction(dispatch, select, isLegacy) {
	const action = {
		id: 'cti',
		select: select
	};
	if (isLegacy) {
		dispatch(UPDATE_MODULE_STATUS, action);
	} else {
		dispatch('HANDLE_OPENED_PROPERTY_CHANGE', select);
		dispatch('PRIME_NAV_SOURCE_CHANGED', action);
	}
}
