import { default as data_read } from './data.read';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
    LOAD_VALUES,
    POPULATE_VALUES,
    CATEGORY_SELECTED,
    POPULATE_CHILD_RESULTS,
    CASE_TYPE_SELECTED,
    SET_STATE,
    FILTER_SUBTYPES
} from '../constants';
import { actionTypes } from '@servicenow/ui-core';
import { 
    onSearchTermChange, 
    onSelectionChange, 
    dispatchPropsToModal,
    dispatchPreSelectedPropsToModal 
} from './utilities';
import { t } from 'sn-translate';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const sortByLabel = function(a, b) {
    var labelA = a.displayValue.toUpperCase();
    var labelB = b.displayValue.toUpperCase();
    if (labelA == t('ALL')) return -1;
    if (labelB == t('ALL')) return 1;
    return ((labelA <= labelB) ? -1 : 1); 
};

var allCaseTypes = [];
var allSubtypes = [];
var resultObject = {};

const loadValues = createGraphQLEffect(data_read, {
    successActionType: POPULATE_VALUES,
    templateVarList: ['schema','function','result','sourceTable', 'sourceId'],
    variableList: ['sourceTable','sourceId']
});

const populateValues = ({state, action, dispatch, updateProperties}) => {
    resultObject = JSON.parse(action.payload.data.AppCaseTypesSelector_Query.getCaseTypes);
    let allCategoryVal = {value: '-1', displayValue: t('All')};
    var categoryData = [allCategoryVal];
    var caseTypesData = [];
    var subTypesData = [];
    for (let categoryItem in resultObject){
        if(categoryItem !== '0')
            categoryData.push({ value: categoryItem, displayValue: resultObject[categoryItem].category_label })

        resultObject[categoryItem].case_types.forEach(function (caseTypeItem){
            caseTypesData.push({ value: caseTypeItem.table_name, 
                                 displayValue: caseTypeItem.label, 
                                 secondary: caseTypeItem.short_description != '' ? [resultObject[categoryItem].category_label, caseTypeItem.short_description] 
                                                                                 : [resultObject[categoryItem].category_label], 
                                 subtypeLabel: caseTypeItem.subtype.label });
            var subtypeVals = []
            // do not insert subtypes for case types that don't have any
            if(caseTypeItem.subtype.values.length > 0 && (caseTypeItem.subtype.values[0].value != '' && caseTypeItem.subtype.values[0].id != '')) {
                caseTypeItem.subtype.values.forEach(function(subtypeItem) {
                    subtypeVals.push({
                        value: subtypeItem.id,
                        displayValue: subtypeItem.value,
                        parentCase: caseTypeItem.table_name,
                        subtypeField: caseTypeItem.subtype.field,
                        parentCaseDisplay: caseTypeItem.label
                    });
                });
            }
            subTypesData.push({parentCase: caseTypeItem.table_name, parentCaseDisplay: caseTypeItem.label,
                               subtypeLabel: caseTypeItem.subtype.label != '' ? caseTypeItem.subtype.label : '', 
                               subtypeField: caseTypeItem.subtype.field, values: subtypeVals});
        })
    }
    categoryData.sort(sortByLabel);

    caseTypesData.sort(sortByLabel);

    if(subTypesData.values[0] && subTypesData.values[0].value != '')
        subTypesData.values.sort(sortByLabel);

    allCaseTypes = [...caseTypesData];

    allSubtypes = [...subTypesData];

    const preSelectItem = () => {
        if(caseTypesData.length == 1){
            dispatchPreSelectedPropsToModal(caseTypesData[0].value, dispatch, caseTypesData[0].displayValue);
            if(caseTypesData[0].subtypeLabel != '') {
                updateProperties({ subtypes: { label: t(caseTypesData[0].subtypeLabel), required: false } });
            }
            return caseTypesData[0];
        } 
        else
            return { value: '', displayValue: '' };
    }
    dispatch(SET_STATE, {
        parentResults: categoryData,
        childResults: caseTypesData,
        subtypeResults: (caseTypesData.length == 1 && allSubtypes[0].values.length > 0) ? allSubtypes[0].values : [],
        defaultChildResults: caseTypesData,
        defaultSubtypeResults: subTypesData,
        searchTerm: caseTypesData.length == 1 ? caseTypesData[0].displayValue : '',
        subtypeToggled: (caseTypesData.length == 1 && allSubtypes[0].values.length > 0) ? true : false,
        selectedSubtypeField: (caseTypesData.length == 1 && allSubtypes[0].values.length > 0) ? allSubtypes[0].subtypeField : ''
    });

    updateProperties({
        selectedCategory: (caseTypesData.length == 1 && categoryData.length == 2) ? categoryData[1] : allCategoryVal,
        selectedCaseTypes: preSelectItem()
    });
};

const categorySelected = ({ state, action, dispatch, updateProperties }) => {
    let filteredResults = [];
    if (state.properties.caseTypes && state.properties.selectedCategory && state.properties.selectedCategory.value != -1) {
        resultObject[state.properties.selectedCategory.value].case_types.forEach(item => 
            filteredResults.push({value: item.table_name, 
                                  displayValue :item.label, 
                                  secondary: item.short_description != '' ? [state.properties.selectedCategory.displayValue, item.short_description] 
                                                                          : [state.properties.selectedCategory.displayValue], 
                                  subtypeLabel: item.subtype.label})
        );
    }
    else if (state.properties.caseTypes && state.properties.selectedCategory && state.properties.selectedCategory.value == -1){
        filteredResults = [...allCaseTypes];
    }
    filteredResults.sort(sortByLabel);

    dispatch(SET_STATE, {
            childResults: filteredResults,
            defaultChildResults: filteredResults,
            subtypeResults: [],
            defaultSubtypeResults: allSubtypes,
            searchTerm: '',
            subtypeSearchTerm: '',
            subtypeToggled: false 
        });
};


const caseTypeSelected = ({ state, action, dispatch }) => {

};

const filterSubtypes = ({state, action, dispatch, updateProperties}) => {
    var filteredResults = [];
    if(allSubtypes) {
        allSubtypes.forEach(function(subtype) {
            // check if subtype parent case is selected case type
            if(subtype.parentCaseDisplay === state.selectedEntityDisplayVal && subtype.label != '' && subtype.values.length > 0) {
                filteredResults.push(subtype);
            }
        });
        if(filteredResults.length === 0 || (filteredResults.length === 1 && filteredResults[0].value === '')) {
            dispatch(SET_STATE, {
                subtypeResults: [], 
                subtypeToggled: false
            });
        } else {
            updateProperties({ subtypes: { label: t(filteredResults[0].subtypeLabel), required: false } });
            dispatch(SET_STATE, {
                subtypeResults: filteredResults[0].values,
                subtypeToggled: true,
                selectedSubtypeField: filteredResults[0].subtypeField
            });
        }
    }
};

const setStateEffect =
    (({state, action, updateState}) => {
        updateState({
            ...state,
            ...action.payload
        });
    });

export default {
    [LOAD_VALUES]: {
        ...loadValues,
        stopPropagation: true
    },
    [POPULATE_VALUES]: {
        effect: populateValues,
        stopPropagation: true 
    },
    [CATEGORY_SELECTED]: {
        effect: categorySelected,
        stopPropagation: true
    },
    [CASE_TYPE_SELECTED]: {
        effect: caseTypeSelected,
        stopPropagation: true
    },
    [SET_STATE]: {
        effect: setStateEffect,
        stopPropagation: true
    },
    [FILTER_SUBTYPES]: {
        effect: filterSubtypes,
        stopPropagation: true
    },
    [COMPONENT_BOOTSTRAPPED]: {
        effect({ state, dispatch }) {
                dispatch(LOAD_VALUES, {
                sourceTable: (state.properties.sourceTable !== undefined) ? state.properties.sourceTable : '',
                sourceId: (state.properties.sourceId !== undefined) ? state.properties.sourceId : ''
            });
        }
    }
};