const safelyGetValue = (path, obj) =>
	path.reduce((xs, x) => (xs && xs[x] ? xs[x] : null), obj);

const isNotEmpty = value => value && value != '';

const getCurrentContext = (fields, contextCandidates) =>
	contextCandidates.reduce(
		(validContext, currentContext) =>
			isNotEmpty(safelyGetValue([currentContext.contextField, 'value'], fields))
				? currentContext
				: validContext,
		undefined
	);
    
export { safelyGetValue, getCurrentContext };