import { GRID_SIZE, findIndexBy } from './utils';
import { map, sum, range, reduce, gt, lt, countBy } from 'lodash';

export const shaveOffLargest = toChange => sizes => {
	// columns of size 1 cannot be shaved
	if (toChange && toChange > 1) {
		return map(sizes, size => (size === toChange ? size - 1 : size));
	}
	const largestIndex = findIndexBy(sizes, gt);
	return map(sizes, (size, i) => (i === largestIndex ? size - 1 : size));
};

export const addOnToSmallest = toChange => sizes => {
	if (toChange) {
		return map(sizes, size => (size === toChange ? size + 1 : size));
	}
	const smallestIndex = toChange ? toChange : findIndexBy(sizes, lt);
	return map(sizes, (size, i) => (i === smallestIndex ? size + 1 : size));
};

export const getAdjustColumnSizesFn = gridSize => sizes => {
	const total = sum(sizes);
	const error = total - gridSize;

	// figure out what to adjust
	const counts = countBy(sizes);

	let toChange = Object.keys(counts).filter(
		i => i !== 1 && counts[i] === Math.abs(error)
	);

	// If there is an exact match of number of things to change
	// also don't let column sizes drop lower than 1
	toChange = toChange.length === 1 ? parseInt(toChange[0]) : 0;

	const adjustmentFn = error > 0 ? shaveOffLargest : addOnToSmallest;
	return reduce(range(error), adjustmentFn(toChange), sizes);
};

export const adjustColumns = getAdjustColumnSizesFn(GRID_SIZE);
