import {TREE_ITEM_SELECTED,OPEN_MODAL_BUTTON_CLICKED,CLOSE_MODAL_BUTTON_CLICKED} from './constants'
import { createUpdateStateEffect } from '@servicenow/ui-effect-update-state';

export default{
    [TREE_ITEM_SELECTED] : ({action, updateState}) => {
        updateState({
            selectedSoldProduct: action.payload.treeItem
        })
    },

    [OPEN_MODAL_BUTTON_CLICKED]:({state,action, updateState}) => {
        updateState({
            openModal:state.openModal?false:true
        })
    },

    [CLOSE_MODAL_BUTTON_CLICKED]:({state,action, updateState}) => {
        updateState({
            openModal:state.openModal?false:true
        })
    }
} 
