import '@devsnc/sn-form-controls';
import { t } from 'sn-translate';
import {
    CATEGORY_SELECTED,
    CASE_TYPE_SELECTED,
    LOAD_FORM,
    CANCEL_BUTTON_ACTION,
    SET_STATE,
    FILTER_SUBTYPES
} from '../constants';
import { onSearchTermChange, onSelectionChange, onSubtypeSearchTermChange, onSubtypeSelectionChange } from './utilities';

export default (state, {dispatch, updateState, updateProperties}) => {
    const {
		properties: {
        heading,
        category,
        caseTypes,
        subtypes,
        selectedCategory,
        selectedCaseTypes,
        selectedSubtype,
        sourceTable,
        sourceId
		},
        parentResults,
        childResults,
        subtypeResults,
        defaultChildResults,
        defaultSubtypeResults, 
        searchTerm,
        subtypeSearchTerm,
        isFocused,
        selectedEntityDisplayVal,
        selectedSubtypeEntityDisplayValue,
        subtypeToggled,
        selectedSubtypeField
    } = state;

    const value = selectedEntityDisplayVal && selectedEntityDisplayVal.length > 0 ? selectedEntityDisplayVal : searchTerm;

    const subtypeValue = selectedSubtypeEntityDisplayValue && selectedSubtypeEntityDisplayValue.length > 0 ? selectedSubtypeEntityDisplayValue : subtypeSearchTerm

    const selectParent = (choice) => {
        updateProperties({ selectedCategory: choice });
        dispatch('SN_SCRIPTED_MODAL#BUTTON_ENABLED_SET', false);
        updateProperties({ selectedCaseTypes: { value: '', displayValue: '' } });
        updateProperties({ selectedSubtype: { value: '', displayValue: '' } });
        dispatch(CATEGORY_SELECTED, { id: choice.value });
        dispatch(SET_STATE, {selectedEntityDisplayVal: ''});
        dispatch(SET_STATE, {selectedSubtypeEntityDisplayValue: ''});
    }

    var subtypeDropdown = (
        subtypeToggled ? 
        <div className="casetype-styles">
            <now-record-typeahead
                label={subtypes.label}
                required={subtypes.required}
                options={subtypeResults}
                value={subtypeValue}
                displayValue={selectedSubtype.displayValue}
                onValueChange = { (a, b, selectionValue, selectionDisplayValue, event) => {
                    onSubtypeSelectionChange(selectionValue, dispatch, updateProperties, selectionDisplayValue, selectedSubtypeField);
                    }
                }
                onChange={
                    (event) => { onSubtypeSearchTermChange(event, dispatch, updateProperties, subtypeResults, defaultSubtypeResults,
                        selectedSubtypeEntityDisplayValue, selectedCaseTypes.value);
                    }
                }
            />
        </div> : <div></div>
    );

    return (
        <div>
            <div className="header-statement">
                {heading}
            </div>
            <span>
                <div className="category-styles">
                    <sn-record-choice
                        label={category.label}
                        required={category.required}
                        choices={parentResults}
                        value={selectedCategory.value}
                        displayValue={selectedCategory.displayValue}
                        onValueChange={selectParent}
                    />
                </div>
            </span>
            <span>
                <div className="casetype-styles">
                    <now-record-typeahead
                        label={caseTypes.label}
                        required={caseTypes.required}
                        options={childResults}                            
                        value={value}
                        displayValue={selectedCaseTypes.displayValue}
                        onValueChange = { (a, b, selectionValue, selectionDisplayValue, event) => {
                            onSelectionChange(selectionValue, dispatch, updateProperties, selectionDisplayValue);
                            }
                        }
                        onChange={
                            (event) => { onSearchTermChange(event, dispatch, updateProperties, childResults, defaultChildResults,
                                selectedEntityDisplayVal);
                            }
                        }
                    />
                </div>
            </span>
            <span>
                {subtypeDropdown}
            </span>
        </div>
    );
};