import { get } from 'lodash';
import { createUpdateStateEffect } from '@servicenow/ui-effect-update-state';

/** OpenframeAPI header events effect handlers - START */
export const openframeHeaderTitle = ({state, action, updateState}) => {
	let title = get(action, 'payload.title');
	let openframeSetTitle = {};
	openframeSetTitle.title = title;
	if (state.openframeSetTitle != openframeSetTitle) {
		updateState({
			path: 'openframeSetTitle',
			value: openframeSetTitle,
			operation: 'set'
		});
	};
};

export const openframeHeaderSubTitle = ({state, action, updateState}) => {
	let subtitle = get(action, 'payload.subtitle');
	let openframeSetSubtitle = {};
	openframeSetSubtitle.subtitle = subtitle;
	if (state.openframeSetSubtitle != openframeSetSubtitle) {
		updateState({
			path: 'openframeSetSubtitle',
			value: openframeSetSubtitle,
			operation: 'set'
		});
	};
};

export const openframeHeaderTitleIcon = ({state, action, updateState}) => {
	let titleIcon = get(action, 'payload');
	delete titleIcon.meta;
	let openframeSetHeaderTitleIcon = {};
	openframeSetHeaderTitleIcon = titleIcon;
	if (state.openframeSetHeaderTitleIcon != openframeSetHeaderTitleIcon) {
		updateState({
			path: 'openframeSetHeaderTitleIcon',
			value: openframeSetHeaderTitleIcon,
			operation: 'set'
		});
	};
};

export const openframeFooterIcons = ({ action, updateState, state}) => {
	let icons = get(action, 'payload');
	delete icons.meta;
	let openframeSetIcons = {};
	openframeSetIcons = icons;
	updateState({
		openframeSetIcons
	});
};
/** OpenframeAPI header events effect handlers - END */
