import {nowHeading} from "@servicenow/now-heading";
import {nowLabelValue} from  "@servicenow/now-label-value";
import { t } from 'sn-translate';
import {OPEN_MODAL_BUTTON_CLICKED} from './constants';
import '../sn-form-field-value';

export function getFormItems(state, itemsLeft, itemsRight){
    if(!state.productDetails)
        return;
    var fieldsList = state.properties.fields.split(",");
    for(var i=0;i<fieldsList.length;i++){
        if(i<fieldsList.length/2){
            itemsLeft.push({
                label : state.productDetails[fieldsList[i]]['label'],
                value : 
                {   type: 'jsx',
                    value:  <sn-form-field-value value={state.productDetails[fieldsList[i]]['displayValue']}></sn-form-field-value>
                }
            })
        }
        else{
            itemsRight.push({
                label : state.productDetails[fieldsList[i]]['label'],
                value : 
                {   type: 'jsx',
                    value:  <sn-form-field-value value={state.productDetails[fieldsList[i]]['displayValue']}></sn-form-field-value>
                }
            })
        }
    }
}

const view = (state, dispatch) => {
var itemsLeft=[];
var itemsRight=[];
getFormItems(state, itemsLeft, itemsRight);
 return(
    state.productDetails?
    <div className = "form-content">
        <div className="row">
        <div className="column">
            
        <h3 className="heading">{state.productDetails['name']['displayValue']}</h3>
            
       <div className="button-iconic">
           
        <now-button
			tooltip-content={t("Product Model for ") + state.productDetails['name']['displayValue']}
			icon="tree-view-long-outline"
			size="sm"
            variant="secondary"
            config-aria={{
				'aria-label': 'Product Model Hierarchy'
			}}
            on-click = {() => {
                dispatch(OPEN_MODAL_BUTTON_CLICKED)
            }} 
		/>
        
        </div>
        </div>
        </div> 
        <div className="form-container">
            <div className="form-left">
                <now-label-value-stacked
                    items= {itemsLeft}
                    align = "vertical"
                />
            </div>
            <div className="form-right">
                <now-label-value-stacked
                    items={itemsRight}
                align = "vertical"
                />
            </div>
        </div>
        
    </div> 
        
    : null
);

}

export default view;