import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { isEmpty as _isEmpty} from 'lodash';
import { userSchema } from "./schemas/userSchema";
import { configurationSchema } from './schemas/configurationSchema';
import { configSchema } from './schemas/configSchema';
import { urlSchema } from './schemas/urlSchema';
import rtlBehavior from '@servicenow/behavior-rtl';

const {
	COMPONENT_CONNECTED,
	COMPONENT_DISCONNECTED,
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;



const snOpenframeUxb = (state, {dispatch}) => {
	const { user, configuration, opened, number = '', table = '', sysId = '', source='cti_button' } = state.properties;
	return !_isEmpty(configuration)
		? (
			<sn-component-openframe
				user={user}
				configuration={configuration}
				isLegacy={false}
				opened={opened}
				number = {number}
				table = {table}
				sysId =  {sysId}
				source = {source}
			/>
		)
		: null;
};

/**
 * OpenFrame provides a communication frame that customer service agents use to place and receive customer calls.
 *
 *  *Openframe CTI (Computer Telephony Interface) use cases*
 *  - OpenFrame provides support for embedded and contextual phone conversations, including:
 *	- Context identification: incoming calls maximize the OpenFrame window and display details about the caller,
 *    including the account, contact or consumer name, and phone number.
 *  - Links to forms: use the openFrameAPI (openServiceNowForm) to create links to forms. When an incoming call is
 *    received, an agent can click the account, contact, consumer, or case number in the OpenFrame window to open
 *    the corresponding record.
 *		- In Agent Workspace, records open in either a primary tab or a sub-tab depending on the tab configuration.
 *		- In the platform interface, records open in TopFrame (i.e., the current page).
 *	- Click-to-call capability: click phone number fields to make a call.
 *		- In Agent Workspace, agents can click phone number fields on forms and in the Customer 360 ribbon component
 *		  to make a call.
 *		- In the platform interface, agents can click the phone icon next to the Contact or Consumer fields.
 *			- If a contact or consumer has one phone number, the call is placed to that number.
 *			- If a contact or consumer has more than one phone number, a dialog box displays the available numbers.
 *			Click a number to place the call.
 *
 * **Usage**
 * ```jsx
 *  <sn-openframe-uxb user={user} configuration={configuration} opened={opened} isLegacy={false} number={number} source={source} table={table} sysId={sysId}/>
 * ```
 *
 * @seismicElement sn-openframe-uxb
 * @summary An openframe UI builder component that provides communication frame for customer service agents
 * to place and receive customer calls.
 */
createCustomElement('sn-openframe-uxb', {
	properties: {
		/**
		 * An object of type sys user item. Used by openframe to get user properties
		 * and values.
		 * @type {Object.<User>}
		 */
		user: { default: {}, schema: userSchema },
		/**
		 * Defines the openframe configuration.
		 * @type {Object.<Configuration>}
		 */
		configuration: { default: {}, schema: configurationSchema },
		/**
		 * Determines component visibility.
		 * @type {boolean}
		 */
		opened: { default: false, schema: { type: 'boolean' } },
		/**
		 * Phone number including the country code.
		 * 	- e.g. `+12223334445`
		 *
		 * @type {string}
		 */
		number: { default: null, schema: { type: 'string' } },
		/**
		 * sysId of the table record.
		 * @type {string}
		 */
		sysId: { default: null, schema: { type: 'string' } },
		/**
		 * Table name.
		 * @type {string}
		 */
		table: { default: null, schema: { type: 'string' } },
		/**
    	* Determines the source for the open phone event.
		* @type {('cti_button'|'ribbon'|'form_field')}
		*/
		source: {
			default: 'cti_button',
			schema: { type: 'string', enum: ['cti_button', 'ribbon', 'form_field'] }
		}
	},
	renderer: {
		type: snabbdom,
		view: snOpenframeUxb
	},
	dispatches: {
		/**
		 * Dispatched to update the toolbar's phone button information.
		 * @type {{id: 'cti', select: true, show: true, icon: 'phone-outline', status: 'available'}}
		 */
		PRIME_NAV_SOURCE_CHANGED: {},
	},
	actionHandlers: {
		['SN_OPENFRAME_UXB#INTERNAL_ITEM_SELECTED']: {effect:({action, dispatch })=>{
			dispatch('SN_OPENFRAME_UXB#RECORD_OPENED', action.payload);
		},stopPropagation : true },
		[COMPONENT_PROPERTY_CHANGED]: (coeffects) => {
			// TODO: remove the next 2 lines after full integration test.
			const {action, payload, state, dispatch, property} = coeffects;
			const {name, value } = coeffects.action.payload;
		},
		['HANDLE_OPENED_PROPERTY_CHANGE']:({action, updateProperties})=>{
			updateProperties({opened: action.payload.value});
		}
		// TODO: Reset the values here once a call is placed.
		// ['UNSET_CLICK_TO_CALL_PAYLOAD']: ({updateProperties, state, action})=>{
		// 	console.log("Update properties - 1", state, action)
		// 	updateProperties({
		// 		number: '',
		// 		sysId: '',
		// 		table:'',
		// 		source:''
		// 	});
		// }
	},
	behaviors: [rtlBehavior]

});
