import { UPDATE_ACCOUNT } from '../sn-component-account-hierarchy/actions';
import { removePath } from '../sn-component-account-hierarchy/utilities';

// Handle a request for update in response to tree control actions
const handleUpdateRequested = ({ state, action, dispatch, updateState }) => {
  let childrenArray = [];
  action.payload.item.children.map(item => {
    childrenArray.push(item.id);
  });
  let { loadingPath } = state;
  const { maxChildren } = state.properties;
  let path = action.payload.path;

  loadingPath = [...removePath(loadingPath, path), {path}];
  updateState({
    operation: 'set',
    path: 'loadingPath',
    value: loadingPath
  });

  dispatch(UPDATE_ACCOUNT, {
    sys_id: action.payload.item.id,
    children: childrenArray,
    updatePath: action.payload.path,
    update: true,
    maxChildren
  });
};
  
const handleSelectedItemsSet = ({ action, updateState }) => {
  let value = action.payload.value;
  if (value == null || value.length == 0) {
    return;
  }

  let path = value[value.length - 1];
  if (path == null || path.length == 0) {
    return;
  }

  let item = path[path.length - 1];
  updateState({
    operation: 'set',
    path: 'clickedId',
    value: item
  });
}

export const actionHandlers = {
  'NOW_CONTENT_TREE#ACTIONABLE_ITEM_CLICKED': ({ action, updateState }) => {
    updateState({
      operation: 'set',
      path: 'clickedId',
      value: action['payload']['item']['id']
    });
  },
  'NOW_CONTENT_TREE#SELECTED_ITEMS_SET': handleSelectedItemsSet,
  'NOW_TOGGLE#CHECKED_SET': ({ action, updateState }) => {
    updateState({
      operation: 'set',
      path: 'parentViewOnly',
      value: action['payload']['value']
    });
  },
  'NOW_CONTENT_TREE#LOADING_REQUESTED': handleUpdateRequested,
  'NOW_CONTENT_TREE#EXPANDED_ITEMS_SET': ({ state, action, updateState }) => {
    let expanded = action.payload.value;
    
    let path = 'expanded';
    if (state.parentViewOnly) {
      path = 'parentExpanded';
    }

    updateState({
      operation: 'set',
      path,
      value: expanded
    });
  },
};